import React, { useEffect } from 'react';
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { convertToShamsi } from '../../../utils/shamsi';
import { getAllChannels } from '../../../actions/Channels';
import qs from 'qs';

const ChannelsTable = () => {
    const dispatch = useDispatch();

    const channels = useSelector(state => state.channels);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllChannels(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | شبکه ها</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        شبکه ها

                        <a href={`/admin/channels/add-channel`} className='btn btn-success'>
                            شبکه جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {channels.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>تاریخ ساخت</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {channels.map(channel => (
                                                    <tr key={channel._id}>
                                                        <td>
                                                            <a target={'_blank'} href={`/movies/channels/${channel._id}`}>{channel.title}</a>
                                                        </td>
                                                        <td>{convertToShamsi(channel.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/channels/edit-channel/${channel._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a
                                                                href={`/admin/channels/delete-channel/${channel._id}`}
                                                                className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>شبکه ای جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default ChannelsTable;