import React, { Fragment, useEffect } from 'react'
import { Helmet } from 'react-helmet';
import PopularMovies from './PopularMovies';
import NewestMovies from './NewestMovies';
import { useSelector, useDispatch } from 'react-redux';
import { orderBy } from 'lodash';
import { getAllMovies } from '../../actions/Movies';
import { getAllSeries } from '../../actions/Series';
import NewestSeries from './NewestSeries';
import PopularSeries from './PopularSeries';

const HomePage = () => {
    const dispatch = useDispatch();

    const movies = useSelector(state => state.movies);
    const newestMovies = orderBy(movies, "createdAt", "desc").slice(0, 4);
    const popularMovies = orderBy(movies, "popularity", "desc").slice(0, 4);

    const series = useSelector(state => state.series);
    const newestSeries = orderBy(series, "createdAt", "desc").slice(0, 4);
    const popularSeries = orderBy(series, "popularity", "desc").slice(0, 4);

    useEffect(() => {
        dispatch(getAllMovies());
        dispatch(getAllSeries());
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند
                </title>
            </Helmet>

            <main id="home-page">
                <div className="container">
                    {movies.length > 0
                        ? (
                            <Fragment>
                                <NewestMovies movies={newestMovies} />
                                <hr />
                                <PopularMovies movies={popularMovies} />
                            </Fragment>
                        )
                        : (
                            <div className="alert alert-info">
                                <p>فیلمی جهت نمایش وجود ندارد !</p>
                            </div>
                        )}
                </div>
            </main>

            <hr />

            <main id="home-page">
                <div className="container">
                    {series.length > 0
                        ? (
                            <Fragment>
                                <NewestSeries series={newestSeries} />
                                <hr />
                                <PopularSeries series={popularSeries} />
                            </Fragment>
                        )
                        : (
                            <div className="alert alert-info">
                                <p>سریالی جهت نمایش وجود ندارد !</p>
                            </div>
                        )}
                </div>
            </main>
        </Fragment>
    );
}

export default HomePage;