import React, { Fragment, useEffect } from 'react'
import { Helmet } from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { getAllGenres } from '../../actions/Genres';

const Gneres = () => {
    const dispatch = useDispatch();
    const genres = useSelector(state => state.genres);

    useEffect(() => {
        dispatch(getAllGenres());
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | ژانر ها
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/movies/genres">ژانر ها</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <aside className="col-md-12 col-sm-12 col-xs-12">
                            <div className="statistics">
                                <h2>
                                    ژانر ها
                                </h2>
                                <hr />

                                {genres.map(genre => (
                                    <a href={`/movies?genre=${genre.title}`} key={genre._id}>
                                        {genre.title}
                                    </a>
                                ))}
                            </div>
                        </aside>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default Gneres;