import { toast } from "material-react-toastify";
import { editActor, getActors, newActor, removeActor } from "../Services/actorService"
import { getMovieActors } from "../Services/movieService";
import { getSerialActors } from "../Services/serialService";

export const getAllActors = searchText => {
    return async dispatch => {
        const { data } = await getActors(searchText);
        await dispatch({ type: "GET_ACTORS", payload: data.actors });
    }
}

export const getSingleMovieActors = movieId => {
    return async dispatch => {
        const { data } = await getMovieActors(movieId);
        await dispatch({ type: "GET_ACTORS", payload: data.actors });
    }
}

export const getSingleSerialActors = serialId => {
    return async dispatch => {
        const { data } = await getSerialActors(serialId);
        await dispatch({ type: "GET_ACTORS", payload: data.actors });
    }
}

export const addActor = actor => {
    return async(dispatch, getState) => {
        const actors = [...getState().actors];

        try {
            const { data, status } = await newActor(actor);

            if (status === 201) {
                await dispatch({ type: "ADD_ACTOR", payload: data.actors });
                toast.success("بازیگر با موفقیت اضافه شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_ACTOR", payload: actors });
            toast.error("عملیات اضافه کردن بازیگر با مشکل مواجه شد!");
        }
    }
}

export const updateActor = (actor, actorId) => {
    return async(dispatch, getState) => {
        const actors = [...getState().actors];

        try {
            const { data, status } = await editActor(actor, actorId);

            if (status === 200) {
                await dispatch({ type: "UPDATE_ACTOR", payload: data.actors });
                toast.success("بازیگر مورد نظر با موفقیت ویرایش شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "UPDATE_ACTOR", payload: actors });
            toast.error("عملیات ویرایش بازیگر با مشکل مواجه شد!");
        }
    }
}

export const deleteActor = actorId => {
    return async(dispatch, getState) => {
        const actors = [...getState().actors];

        try {
            const { data, status } = await removeActor(actorId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_ACTOR", payload: data.actors });
                toast.success("بازیگر موردنظر با موفقیت پاک شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "REMOVE_ACTOR", payload: actors });
            toast.error("عملیات پاکسازی بازیگر با مشکل مواجه شد !");
        }
    }
}