import { toast } from "material-react-toastify";
import { addSerialDownloads, getSerial, reactToSerial } from "../Services/serialService";

export const getSingleSerial = serialId => {
    return async dispatch => {
        try {
            const { data } = await getSerial(serialId);
            await dispatch({ type: "GET_SERIAL", payload: data.serial });
        } catch (err) {
            console.log(err);
            toast.error("سریال مورد نظر پیدا نشد !");
        }
    }
}

export const reactionToSerial = (serialId, method) => {
    return async dispatch => {
        try {
            const { data, status } = await reactToSerial(serialId, method);

            if (status === 200) {
                await dispatch({ type: "EDIT_SERIAL", payload: data.serial });
                toast.success("نظر كاربر با موفقيت ثبت شد");
            }
        } catch (err) {
            console.log(err);
            toast.error("نظرسنجي كاربر با مشكل مواجه شد!");
        }
    }
}

export const addSerialDownload = serialId => {
    return async dispatch => {
        try {
            const { data, status } = await addSerialDownloads(serialId);

            if (status === 200) {
                await dispatch({ type: "EDIT_SERIAL", payload: data.serial });
                toast.success("سریال مورد نظر با موفقیت دانلود شد !");
            }
        } catch (err) {
            console.log(err);
            toast.error("دانلود سریال مورد نظر با مشکل مواجه شد !");
        }
    }
}