import React, { Fragment, useRef, useState } from 'react'
import { Helmet } from 'react-helmet';
import { loginUser } from '../../Services/userService';
import { toast } from 'material-react-toastify';
import { Redirect, withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { useDispatch, useSelector } from 'react-redux';
import { setUser } from '../../actions/User';
import { random } from 'lodash';
// import { decodeToken } from '../../utils/decodeToken';

const Login = ({ history }) => {
    const user = useSelector(state => state.user);

    const [email,
        setEmail] = useState("");
    const [password,
        setPassword] = useState("");
    const [remember,
        setRemember] = useState(false);
    const [,
        forceUpdate] = useState(0);

    const dispatch = useDispatch();

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: " پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 5 کاراکتر باشد!",
            email: "ایمیل وارد شده معتبر نمی باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const resetUser = () => {
        setEmail("");
        setPassword("");
        setRemember("");
    }

    const handleLogin = async event => {
        event.preventDefault();

        const user = {
            email,
            password,
            remember
        }

        try {
            if (validator.current.allValid()) {
                const { status, data } = await loginUser(user);

                if (status === 200) {
                    localStorage.setItem("token", data.token);
                    localStorage.setItem("userId", data.userId);
                    toast.success("ورود کاربر موفقیت آمیز بود!");
                    // dispatch(setUser(decodeToken(data.token).payload.user));
                    dispatch(setUser(data.user));
                    resetUser();
                    history.push("/");
                }
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 100));
            }
        } catch (err) {
            toast.error("ایمیل یا رمز عبور اشتباه می باشد !");
            console.log(err);
        }
    }

    if (user.fullname)
        return <Redirect to={"/"} />

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | ورود
                </title>
            </Helmet>

            <div className="container">
                <nav aria-label="breadcrumb">
                    <ul className="breadcrumb">
                        <li className="breadcrumb-item">
                            <a href="/">مووی لند</a>
                        </li>
                        <li className="breadcrumb-item active">
                            <a href="/user/login">ورود به سايت</a>
                        </li>
                    </ul>
                </nav>
            </div>

            <main className="client-page">

                <div className="container-content">

                    <header>
                        <h2>
                            ورود به
                            <span>مووی لند</span>
                        </h2>
                    </header>

                    <div className="form-layer">

                        <form onSubmit={e => handleLogin(e)}>

                            <div className="input-group">
                                <span className="input-group-text" id="email-address">
                                    ایمیل
                                </span>
                                <input
                                    type="text"
                                    name='email'
                                    value={email}
                                    onChange={e => {
                                        setEmail(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("email");
                                    }}
                                    className="form-control"
                                    placeholder="@"
                                    aria-describedby="email-address" />
                            </div>
                            {validator
                                .current
                                .message("email", email, "required|email")}

                            <div className="input-group">
                                <span className="input-group-text" id="password">
                                    رمز عبور
                                </span>
                                <input
                                    type="password"
                                    name='password'
                                    value={password}
                                    onChange={e => {
                                        setPassword(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("password");
                                    }}
                                    className="form-control"
                                    placeholder="******"
                                    aria-describedby="password" />
                            </div>
                            {validator
                                .current
                                .message("password", password, "required|min:6")}

                            <div className="form-check">
                                <input
                                    id="flexCheckDefault"
                                    className="form-check-input"
                                    type="checkbox"
                                    name='remember'
                                    value={remember}
                                    onClick={e => {
                                        setRemember(e.target.checked);
                                        validator
                                            .current
                                            .showMessageFor("remember");
                                    }} />

                                <label className="form-check-label" for="flexCheckDefault">
                                    مرا به خاطر بسپار
                                </label>
                            </div>
                            {validator
                                .current
                                .message("remember", remember, "required")}

                            <div className="link">
                                <a href="/user/forgot-password">
                                    <i className="zmdi zmdi-lock"></i>
                                    رمز عبور خود را فراموش کرده ام !</a>
                                <a href="/user/register">
                                    <i className="zmdi zmdi-account"></i>
                                    عضویت در مووی لند
                                </a>
                            </div>

                            <div className="input-group">
                                <div
                                    className="g-recaptcha"
                                    data-sitekey="6LdM7HghAAAAANM-6frxXMB7CW7xm1IAnU7Af9Z9"
                                    data-theme="dark"></div>
                            </div>

                            <button className="btn btn-success">
                                ورود به مووی لند
                            </button>

                        </form>
                    </div>

                </div>
            </main>
        </Fragment>
    );
}

export default withRouter(Login);