import { random } from 'lodash';
import { toast } from 'material-react-toastify';
import React, { Fragment, useRef, useState } from 'react';
import { Helmet } from 'react-helmet';
import { useSelector } from 'react-redux';
import { Redirect, withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { registerUser } from '../../Services/userService';

const Register = ({ history }) => {
    const user = useSelector(state => state.user);

    const [fullname,
        setFullname] = useState("");
    const [email,
        setEmail] = useState("");
    const [password,
        setPassword] = useState("");
    const [confirmPassword,
        setConfirmPassword] = useState("");
    const [policy,
        setPolicy] = useState(false);
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: " پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 5 کاراکتر باشد!",
            email: "ایمیل وارد شده معتبر نمی باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const resetUser = () => {
        setFullname("");
        setEmail("");
        setPassword("");
        setConfirmPassword("");
    }

    const handleRegister = async event => {
        event.preventDefault();

        const user = {
            fullname,
            email,
            password,
            confirmPassword
        }

        try {
            if (validator.current.allValid()) {
                if (password === confirmPassword) {
                    if (policy) {
                        const { status } = await registerUser(user);

                        if (status === 201) {
                            toast.success("کاربر با موفقیت ساخته شد!");
                            resetUser();
                            history.push("/user/login");
                        }
                    } else {
                        toast.error("موافق بودن با قوانین مووی لند الزامی است!");
                    }
                } else {
                    toast.error("کلمات عبور یکسان نیستند !");
                }
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 100));
            }
        } catch (err) {
            toast.error(JSON.parse(err.request.response).message);
            console.log(err);
        }
    }

    if (user.fullname)
        return <Redirect to={"/"} />

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | ثبت نام
                </title>
            </Helmet>

            <div className="container">
                <nav aria-label="breadcrumb">
                    <ul className="breadcrumb">
                        <li className="breadcrumb-item">
                            <a href="/">مووی لند</a>
                        </li>
                        <li className="breadcrumb-item active">
                            <a href="/user/register">ثبت نام</a>
                        </li>
                    </ul>
                </nav>
            </div>

            <main className="client-page">
                <div className="container-content">

                    <header>
                        <h2>
                            عضویت در
                            <span>مووی لند</span>
                        </h2>
                    </header>

                    <div className="form-layer">
                        <form onSubmit={e => handleRegister(e)}>
                            <div className="input-group">
                                <span className="input-group-text" id="username">
                                    نام کامل
                                </span>
                                <input
                                    type="text"
                                    name='fullname'
                                    value={fullname}
                                    onChange={e => {
                                        setFullname(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("fullname");
                                    }}
                                    className="form-control"
                                    aria-describedby="username" />
                            </div>
                            {validator
                                .current
                                .message("fullname", fullname, "required|min:4")}

                            <div className="input-group">
                                <span className="input-group-text" id="email-address">
                                    ایمیل
                                </span>
                                <input
                                    type="email"
                                    name='email'
                                    value={email}
                                    onChange={e => {
                                        setEmail(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("email");
                                    }}
                                    className="form-control"
                                    aria-describedby="email-address" />
                            </div>
                            {validator
                                .current
                                .message("email", email, "required")}

                            <div className="input-group">
                                <span className="input-group-text" id="password">
                                    رمز عبور
                                </span>
                                <input
                                    type="password"
                                    name='password'
                                    value={password}
                                    onChange={e => {
                                        setPassword(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("password");
                                    }}
                                    className="form-control"
                                    aria-describedby="password" />
                            </div>
                            {validator
                                .current
                                .message("password", password, "required|min:6")}

                            <div className="input-group">
                                <span className="input-group-text" id="password">
                                    تایید رمز
                                </span>
                                <input
                                    type="password"
                                    name="confirmPassword"
                                    className="form-control"
                                    aria-describedby="confirmPassword"
                                    value={confirmPassword}
                                    onChange={e => {
                                        setConfirmPassword(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("confirmPassword");
                                    }} />
                            </div>
                            {validator
                                .current
                                .message("confirmPassword", confirmPassword, "required|min:6")}

                            <div className="input-group">
                                <div className="form-check">
                                    <input
                                        id="accept-roles"
                                        className="form-check-input"
                                        type="checkbox"
                                        name="policy"
                                        value={policy}
                                        onChange={e => {
                                            setPolicy(e.target.checked);
                                            validator
                                                .current
                                                .showMessageFor("policy");
                                        }} />

                                    <label className="form-check-label" for="accept-roles">
                                        قوانین و مقررات سایت را میپذیرم
                                    </label>
                                </div>
                            </div>
                            {validator
                                .current
                                .message("policy", policy, "required")}

                            <div className="input-group">
                                <div className="link">
                                    <a href="/about-us">
                                        <i className="zmdi zmdi-assignment"></i>
                                        قوانین و مقررات سایت !</a>
                                    <a href="/user/login">
                                        <i className="zmdi zmdi-account"></i>
                                        ورود به سایت
                                    </a>
                                </div>
                            </div>

                            <div className="input-group">
                                <div
                                    className="g-recaptcha"
                                    data-sitekey="6LdM7HghAAAAANM-6frxXMB7CW7xm1IAnU7Af9Z9"
                                    data-theme="dark"></div>
                            </div>

                            <button className="btn btn-success">
                                عضویت در سایت
                            </button>
                        </form>
                    </div>
                </div>
            </main>
        </Fragment>
    );
}

export default withRouter(Register);