import { editGenre, getGenres, newGenre, removeGenre } from "../Services/genreService";
import { toast } from "material-react-toastify";

export const getAllGenres = searchText => {
    return async dispatch => {
        try {
            const { data } = await getGenres(searchText);
            await dispatch({ type: "GET_GENRES", payload: data.genres });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری ژانر ها با مشکل مواجه شد !");
        }
    }
}

export const addGenre = genre => {
    return async dispatch => {
        try {
            const { data, status } = await newGenre(genre);

            if (status === 201) {
                await dispatch({ type: "ADD_GENRE", payload: data.genres });
                toast.success("ژانر مورد نظر با موفقیت اضافه شد !");
            }
        } catch (err) {
            console.log(err);
            toast.error("عملیات اضافه کردن ژانر با مشکل مواجه شد !");
        }
    }
}

export const updateGenre = (genreId, genre) => {
    return async(dispatch, getState) => {
        const genres = [...getState().genres];

        try {
            const { data, status } = await editGenre(genreId, genre);

            if (status === 200) {
                await dispatch({ type: "EDIT_GENRE", payload: data.genres });
                toast.success("ویرایش ژانر مورد نظر موفقیت آمیز بود.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "EDIT_GENRE", payload: genres });
            toast.error("عملیات ویرایش ژانر با مشکل مواجه شد !");
        }
    }
}

export const deleteGenre = genreId => {
    return async(dispatch, getState) => {
        const genres = [...getState().genres];

        try {
            const { data, status } = await removeGenre(genreId);

            if (status === 200) {
                await dispatch({ type: "DELETE_GENRE", payload: data.genres });
                toast.success("ژانر مورد نظر با موفقیت پاک شد.");
            } else if (status === 404) {
                toast.error("ژانر مورد نظر پیدا نشد !");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "DELETE_GENRE", payload: genres });
            toast.error("عملیات پاکسازی ژانر با مشکل مواجه شد !");
        }
    }
}