import { toast } from "material-react-toastify";
import { changeRole, getUser, getUsers, removeUser, updateUser } from "../Services/usersService"

export const getAllUsers = searchText => {
    return async dispatch => {
        const { data } = await getUsers(searchText);
        await dispatch({ type: "GET_USERS", payload: data.users });
    }
}

export const changeUserRole = user => {
    return async(dispatch, getState) => {
        const users = [...getState().users];

        try {
            const { data, status } = await changeRole(user);

            if (status === 200) {
                await dispatch({ type: "UPDATE_USERS", payload: data.users });
                toast.success("نقش کاربر مورد نظر با موفقیت ویرایش شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "UPDATE_USERS", payload: users });
            toast.error("عملیات ویرایش کاربر با مشکل مواجه شد!");
        }
    }
}

export const updateUSER = (user, userId) => {
    return async(dispatch, getState) => {
        const users = [...getState().users];

        try {
            const { data, status } = await updateUser(user, userId);

            if (status === 200) {
                await dispatch({ type: "EDIT_USER", payload: data.users });
                toast.success("مشخصات کاربر با موفقیت ویرایش شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "EDIT_USER", payload: users });
            toast.error("عملیات ویرایش مشخصات کاربر با مشکل مواجه شد !");
        }
    }
}

export const deleteUser = userId => {
    return async(dispatch, getState) => {
        const users = [...getState().users];

        try {
            const { data, status } = await removeUser(userId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_USER", payload: data.users });
                toast.success("کاربر مورد نظر با موفقیت پاک شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "REMOVE_USER", payload: users });
            toast.error("عملیات پاکسازی کاربر با مشکل مواجه شد!");
        }
    }
}