import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Messages
export const getMessages = () => {
    return http.get(`${local}/admin/messages`);
}

// Gets Single Message
export const getMessage = messageId => {
    return http.get(`${local}/admin/messages/${messageId}`);
}

// Sned New Message
export const sendMessage = message => {
    return http.post(`${local}/contact-us`, JSON.stringify(message));
}

// Sned Answer to Message
export const answerMessage = (messageId, answer) => {
    return http.post(`${local}/admin/messages/answer-message/${messageId}`, JSON.stringify(answer));
}

// Removes Message
export const removeMessage = messageId => {
    return http.delete(`${local}/admin/messages/remove-message/${messageId}`);
}