import React from 'react';
import config from '../../Services/config.json';
import { formatDate } from '../../utils/formatDate';

const ActorCard = ({ actor }) => {
    return (
        <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12 term-col">
            <article className='movie-card'>
                <a href={`/actors/${actor._id}`} className="img-layer"><img src={`${config.localapi}/portraits/${actor.portrait}`} /></a>
                <h2>
                    <a href={`/actors/${actor._id}`}>
                        {actor.fullname}
                    </a>
                </h2>
                <span>
                    {new Date(Date.now()).getFullYear() - formatDate(actor.birthDate).getFullYear()}yrs
                </span>
                <i>Actor</i>
            </article>
        </div>
    );
}

export default ActorCard;