import React from 'react';
import SerialCard from '../Card/SerialCard';

const NewestSeries = ({ series }) => {
    return (
        <section className="terms-items">
            <header>
                <h2>جدیدترین سریال ها</h2>
                <a href="/series">
                    مشاهده همه سریال ها
                </a>
            </header>
            <div className="row">
                {series.map(serial => <SerialCard serial={serial} />)}
            </div>
        </section>
    );
}

export default NewestSeries;