import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getSingleMovieActors } from '../../actions/Actors';
import ActorCard from '../Card/ActorCard';

const MovieActors = ({ movieId }) => {
    const dispatch = useDispatch();
    const actors = useSelector(state => state.actors);

    useEffect(() => {
        dispatch(getSingleMovieActors(movieId));
    }, []);

    return (
        <section className="user-comments">
            <header>
                <h3>
                    بازيگران
                </h3>
            </header>
            <div className="inner">
                <div className="row">
                    {actors.map(actor => <ActorCard actor={actor} key={actor._id} />)}
                    {/* <div className="col-xs-12">
                        <a href='/archive' className="btn btn-success">مشاهده همه</a>
                    </div> */}
                </div>
            </div>
        </section>
    );
}

export default MovieActors;