import React, { Fragment } from 'react'
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Episode from './Episode';
import { getRelatedEpisodes } from '../../../actions/Episodes';
import { range } from 'lodash';
import { useState } from 'react';

const Episodes = ({ seasons, serialId }) => {
    const dispatch = useDispatch();

    const [SEASON,
        setSEASON] = useState(1);

    const episodes = useSelector(state => state.episodes);

    const SEASONS = range(1, seasons + 1);

    useEffect(() => {
        dispatch(getRelatedEpisodes(serialId, undefined, SEASON));
    }, [SEASON]);

    return (
        <Fragment>

            <section className="user-comments">
                <header>
                    <h3>
                        دانلود باکس
                    </h3>

                    <div className='input-group'>
                        <span class="input-group-text" id="basic-addon1">فصل</span>
                        <select
                            className='form-select'
                            name="seasons"
                            id="seasons"
                            value={SEASON}
                            onChange={e => setSEASON(e.target.value)}>
                            {SEASONS.map(s => (
                                <option value={s} key={s}>{s}</option>
                            ))}
                        </select>
                    </div>
                </header>

                <br />

                <div className="inner">
                    <div className="row g-3">
                        {episodes.length > 0
                            ? episodes.map(ep => (<Episode ep={ep} />))
                            : (
                                <div className='alert alert-info'>
                                    <p>قسمتی جهت نمایش وجود ندارد !</p>
                                </div>
                            )}
                    </div>
                </div>
            </section>
        </Fragment >
    );
}

export default Episodes;