import React, { useEffect } from 'react'
import { formatDate } from '../../utils/formatDate';
import { padNumber } from '../../utils/padNumbers';
import { useDispatch, useSelector } from 'react-redux';
import { getSingleMovieGenre } from '../../actions/Genre';
import { getSingleMovieChannel } from '../../actions/Channel';
import { getSingleMovieLimit } from '../../actions/AgeLimit';

const MovieInfo = ({ movie, movieId }) => {
    const dispatch = useDispatch();

    const genre = useSelector(state => state.genre);
    const channel = useSelector(state => state.channel);
    const ageRestriction = useSelector(state => state.limit);

    useEffect(() => {
        dispatch(getSingleMovieGenre(movieId));
        dispatch(getSingleMovieChannel(movieId));
        dispatch(getSingleMovieLimit(movieId));
    }, []);

    return (
        <article className="term-info">
            <h2>
                اطلاعات فیلم
            </h2>
            <ul>
                <li>ژانر : {genre.title}</li>
                <li>محدودیت سنی : {ageRestriction.title}</li>
                <li>شبکه : {channel.title}</li>
                <li>تاریخ انتشار : {formatDate(movie.datePublished).getFullYear()}</li>
                <li>مدت زمان فیلم : {`${padNumber(Math.floor(movie.timeLength % 60))} : ${padNumber(Math.floor(movie.timeLength / 60))}`}</li>
                <li>تعداد دانلود ها : {movie.downloads}</li>
            </ul>
        </article>
    );
}

export default MovieInfo;