export const directorsReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_DIRECTORS":
            return [...action.payload];
        case "ADD_DIRECTOR":
            return [...action.payload];
        case "UPDATE_DIRECTOR":
            return [...action.payload];
        case "REMOVE_DIRECTOR":
            return [...action.payload];
        default:
            return state;
    }
}