import React, { Fragment, useRef, useState } from 'react'
import SimpleReactValidator from 'simple-react-validator';
import { addActor } from '../../../actions/Actors';
import { toast } from 'material-react-toastify';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { random } from 'lodash';

const AddActor = ({ history }) => {
    const dispatch = useDispatch();

    const [fullname,
        setFullname] = useState("");
    const [shortInfo,
        setShortInfo] = useState("");
    const [birthDate,
        setBirthDate] = useState("");
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد",
            min: "کمتر از 5 کاراکتر نباید باشد"
        },
        element: message => <div
            className='alert alert-danger'
            style={{
                marginTop: "5px"
            }}>
            {message}
        </div>
    }));

    const handleAddActor = event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                const actor = new FormData();
                actor.append("fullname", fullname);
                actor.append("shortInfo", shortInfo);
                actor.append("birthDate", birthDate);
                actor.append("portrait", document.getElementById("actor-portrait").files[0]);

                dispatch(addActor(actor));
                history.replace("/admin/actors");
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(0, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error(JSON.parse(err.request.response).message);
        }
    }

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | بازیگر جدید</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        بازیگر جدید
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <form className='row g-3' onSubmit={e => handleAddActor(e)}>
                                    <div class="input-group">
                                        <span class="input-group-text" id="basic-addon1">نام کامل</span>
                                        <input
                                            type="text"
                                            name='fullname'
                                            value={fullname}
                                            class="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setFullname(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("fullname");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("fullname", fullname, "required|min:4")}
                                    <br />

                                    <div class="input-group">
                                        <span class="input-group-text" id="basic-addon1">توضیحات</span>
                                        <textarea
                                            name='shortInfo'
                                            value={shortInfo}
                                            class="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setShortInfo(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("shortInfo");
                                            }}></textarea>
                                    </div>
                                    {validator
                                        .current
                                        .message("shortInfo", shortInfo, "required|min:10")}
                                    <br />

                                    <div class="input-group">
                                        <span class="input-group-text" id="basic-addon1">تاریخ تولد</span>
                                        <input
                                            type="date"
                                            name='birthDate'
                                            value={birthDate}
                                            class="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setBirthDate(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("birthDate");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("birthDate", birthDate, "required")}
                                    <br />

                                    <div class="input-group">
                                        <label class="input-group-text" for="actor-portrait">پرتره</label>
                                        <input
                                            type="file"
                                            name='portrait'
                                            class="form-control"
                                            aria-describedby="basic-addon1"
                                            id='actor-portrait' />
                                    </div>
                                    <br />

                                    <div class="input-group">
                                        <button type='submit' className='btn btn-success col-md-3'>
                                            بازیگر جدید
                                        </button>
                                        <button
                                            className='btn btn-warning col-md-3'
                                            style={{
                                                marginRight: "10px"
                                            }}
                                            onClick={() => history.push("/admin/actors")}>
                                            انصراف
                                        </button>
                                    </div>
                                </form>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(AddActor);