import React, { useEffect, useState } from 'react'
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { convertToShamsi } from '../../../utils/shamsi';
import config from '../../../Services/config.json';
import { getAllSeries } from '../../../actions/Series';
import qs from 'qs';

const SeriesTable = ({ location }) => {
    const dispatch = useDispatch();

    const series = useSelector(state => state.series);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllSeries(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | سریال ها</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        سریال ها

                        <a href={`/admin/series/add-serial`} className='btn btn-success'>
                            سریال جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {series.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>قسمت ها</th>
                                                    <th>کاور</th>
                                                    <th>تاریخ عرضه</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {series.map(serial => (
                                                    <tr key={serial._id}>
                                                        <td>
                                                            <a target={'_blank'} href={`/series/${serial._id}`}>{serial.title}</a>
                                                        </td>
                                                        <td>
                                                            <a target={'_blank'} href={`/admin/series/episodes/${serial._id}`}>نمایش همه</a>
                                                        </td>
                                                        <td>
                                                            <a target={'_blank'} href={`${config.localapi}/thumbnails/${serial.thumbnail}`}>{serial.thumbnail}</a>
                                                        </td>
                                                        <td>{convertToShamsi(serial.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/series/edit-serial/${serial._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a href={`/admin/series/delete-serial/${serial._id}`} className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>سریالی جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default SeriesTable;