import React, { useEffect } from 'react';
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { convertToShamsi } from '../../../utils/shamsi';
import { getAllLimits } from '../../../actions/AgeLimits';
import qs from 'qs';

const LimitsTable = () => {
    const dispatch = useDispatch();

    const limits = useSelector(state => state.limits);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllLimits(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | محدودیت ها</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        محدودیت ها

                        <a href={`/admin/age-limits/add-limit`} className='btn btn-success'>
                            محدودیت جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {limits.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>تاریخ ساخت</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {limits.map(limit => (
                                                    <tr key={limit._id}>
                                                        <td>
                                                            <a>{limit.title}</a>
                                                        </td>
                                                        <td>{convertToShamsi(limit.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/age-limits/edit-limit/${limit._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a
                                                                href={`/admin/age-limits/delete-limit/${limit._id}`}
                                                                className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>محدودیتی جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default LimitsTable;