import React, { Fragment, useEffect } from 'react'
import ActorMovies from './ActorMovies';
import Rating from '../../components/common/Rating';
import Share from '../../components/common/Share';
import Tags from '../../components/common/Tags';
import { Helmet } from 'react-helmet';
import { Redirect, withRouter } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { getSingleActor } from '../../actions/Actor';
import config from '../../Services/config.json';
import { idValidator } from '../../utils/idValidator';
import { isEmpty } from 'lodash';
import { getAllMovies } from '../../actions/Movies';
import { getAllSeries } from '../../actions/Series';

const SingleActor = ({ match }) => {
    const actorId = match.params.id;
    const dispatch = useDispatch();
    const actor = useSelector(state => state.actor);
    const movies = useSelector(state => state.movies);
    const series = useSelector(state => state.series);

    let actorMovies = [];
    for (let i = 0; i < movies.length; i++) {
        for (let j = 0; j < movies[i].actors.length; j++) {
            if (movies[i].actors[j]._id === actor._id) {
                actorMovies.push(movies[i]);
            }
        }
    }

    for (let i = 0; i < series.length; i++) {
        for (let j = 0; j < series[i].actors.length; j++) {
            if (series[i].actors[j]._id === actor._id) {
                actorMovies.push(series[i]);
            }
        }
    }

    useEffect(() => {
        if (idValidator(actorId)) {
            dispatch(getSingleActor(actorId));
            dispatch(getAllMovies());
            dispatch(getAllSeries());
        } else {
            return location.replace("/actors");
        }
    }, []);

    // if (isEmpty(actor)) {     return <Redirect to={"/"} />; }

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | بازیگر
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/actors">بازيگران</a>
                            </li>
                            <li className="breadcrumb-item active" aria-current="page">
                                <a href={`/actors/${actor._id}`}>{actor.fullname}</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <div className="col-md-4 col-sm-12 col-xs-12 pull-left">
                            <section className="term-description">
                                <div>
                                    <img src={`${config.localapi}/portraits/${actor.portrait}`} />
                                    <h2>
                                        {actor.fullname}
                                    </h2>
                                    <p>
                                        {actor.shortInfo}
                                    </p>
                                </div>

                            </section>
                        </div>

                        <aside className="col-md-8 col-sm-12 col-xs-12 pull-right">
                            <ActorMovies actorMovies={actorMovies} actorName={actor.fullname} />
                            {/* <Rating /> */}
                        </aside>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(SingleActor);