import React, { useEffect } from 'react'
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router';
import { deleteMessage } from '../../../actions/Messages';
import { idValidator } from '../../../utils/idValidator';

const DeleteMessage = ({ history, match }) => {
    const dispatch = useDispatch();
    const messageId = match.params.id;

    useEffect(() => {
        if (idValidator(messageId)) {
            if (confirm("آیا از حذف کردن این پیام مطئنید ؟")) {
                dispatch(deleteMessage(messageId));
                history.replace("/admin/messages");
            }
            else {
                history.replace("/admin/messages");
            }
        } else {
            return location.replace("/admin/messages");
        }
    }, []);

    return null;
}

export default withRouter(DeleteMessage);