import React, { Fragment, useRef, useState } from 'react'
import SimpleReactValidator from 'simple-react-validator';
import { toast } from 'material-react-toastify';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { random } from 'lodash';
import { addGenre } from '../../../actions/Genres';

const AddGenre = ({ history }) => {
    const dispatch = useDispatch();

    const [title,
        setTitle] = useState("");
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد",
            min: "کمتر از 5 کاراکتر نباید باشد"
        },
        element: message => <div
            className='alert alert-danger'
            style={{
                marginTop: "5px"
            }}>
            {message}
        </div>
    }));

    const handleAddGenre = event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                dispatch(addGenre({ title }));
                history.replace("/admin/genres");
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(0, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error(JSON.parse(err.request.response).message);
        }
    }

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | ژانر جدید</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        ژانر جدید
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <form className='row g-3' onSubmit={e => handleAddGenre(e)}>
                                    <div class="input-group">
                                        <span class="input-group-text" id="basic-addon1">عنوان</span>
                                        <input
                                            type="text"
                                            name='title'
                                            value={title}
                                            class="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setTitle(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("title");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("title", title, "required")}
                                    <br />

                                    <div class="input-group">
                                        <button type='submit' className='btn btn-success col-md-3'>
                                            ژانر جدید
                                        </button>

                                        <button
                                            className='btn btn-warning col-md-3'
                                            style={{
                                                marginRight: "10px"
                                            }}
                                            onClick={() => history.push("/admin/genres")}>
                                            انصراف
                                        </button>
                                    </div>
                                </form>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(AddGenre);