import React, { Fragment } from 'react';

const LandingHeader = () => {
    return (
        <Fragment>
            <header>
                <a href="" className="logo"><img src="images/logo.png" /></a>
                <h1>
                    دسترسی به دنیای جذاب فیلم و سریال
                </h1>
                <h2>
                    انواع فیلم و سریال از Netflix ، HBO ، AMC+ ، Disney+ و ...
                </h2>
                <h3>
                    در کمترین زمان ممکن فیلم مورد علاقه تو پیدا کن
                </h3>
            </header>
            <div className="search-form">
                <form action={`/movies`}>
                    <input type="text" name="search" placeholder="چی دوست داری ببینی ؟" />
                    <button type='submit'>
                        <i className="zmdi zmdi-search"></i>
                    </button>
                </form>
            </div>
        </Fragment>
    );
}

export default LandingHeader;