import React, { Fragment, useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { getAllSeries } from '../../actions/Series';
import Pagination from '../common/Pagination';
import { getAllActors } from '../../actions/Actors';
import { getAllDirectors } from '../../actions/Directors';
import { getAllChannels } from '../../actions/Channels';
import { getAllGenres } from '../../actions/Genres';
import qs from 'qs';
import SerialCard from '../Card/SerialCard';
import { paginate } from '../../utils/paginate';

const Series = () => {
    const dispatch = useDispatch();

    const series = useSelector(state => state.series);
    const directors = useSelector(state => state.directors);
    const actors = useSelector(state => state.actors);
    const genres = useSelector(state => state.genres);
    const chnls = useSelector(state => state.channels);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;
    const GNR = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .genre;
    const SORT = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .sort;
    const DIRECTOR = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .director;
    const ACTOR = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .actor;
    const CHANNEL = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .channel;

    const [ST,
        setST] = useState(searchText);
    const [GENRE,
        setGENRE] = useState(GNR);
    const [sort,
        setSort] = useState(SORT);
    const [director,
        setDirector] = useState(DIRECTOR);
    const [actor,
        setActor] = useState(ACTOR);
    const [channel,
        setChannel] = useState(CHANNEL);
    const [currentPage,
        setCurrentPage] = useState(1);
    const [perPage,
        setPerPage] = useState(8);

    const options = ["جدیدترین", "قدیمی ترین", "محبوب ترین", "منفور ترین"];

    const handlePageChange = page => {
        setCurrentPage(page);
    }

    useEffect(() => {
        dispatch(getAllSeries(searchText, GNR, SORT, DIRECTOR, ACTOR));
        dispatch(getAllDirectors());
        dispatch(getAllActors());
        dispatch(getAllGenres());
        dispatch(getAllChannels());
    }, []);

    const indexedSeries = paginate(series, currentPage, perPage);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | سريال ها</title>
            </Helmet>

            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/series">ژانر</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-categories">

                    <div className="top-bar">

                        <header>
                            {GENRE !== undefined
                                ? (
                                    <h1>
                                        سريال های
                                        <span>
                                            {GENRE}
                                        </span>
                                    </h1>
                                )
                                : (
                                    <h1>
                                        سريال ها
                                    </h1>
                                )}
                            <span>
                                {series.length}
                                {" "}
                                سريال
                            </span>
                        </header>

                        <div className="row g-3">
                            <div className="col-md-4 col-sm-12 col-xs-12 pull-right">
                                <form onSubmit={e => e.preventDefault()}>
                                    <div className="input-group">
                                        <span class="input-group-text" id="basic-addon1">جستجو</span>
                                        <input
                                            className='form-control'
                                            type="text"
                                            name="searchMovie"
                                            id="searchMovie"
                                            placeholder="..."
                                            onChange={e => {
                                                setST(e.target.value);
                                                dispatch(getAllSeries(e.target.value, GENRE, sort, director, actor, channel));
                                            }}
                                            style={{
                                                borderBottomLeftRadius: "5px"
                                            }}
                                        />
                                    </div>
                                </form>
                            </div>

                            <div className="col-md-4 col-sm-6 col-xs-12 pull-left">
                                <div className="input-group">
                                    <span class="input-group-text" id="basic-addon1">مرتب سازی</span>
                                    <select
                                        className='form-select'
                                        onChange={e => {
                                            setSort(e.target.value);
                                            dispatch(getAllSeries(ST, GENRE, e.target.value, director, actor, channel));
                                        }}>
                                        {options.map(op => (
                                            <option value={op} key={op}>
                                                {op}
                                            </option>
                                        ))}
                                    </select>
                                </div>
                            </div>

                            <div className="col-md-4 col-sm-6 col-xs-12 pull-left">
                                <div className="input-group">
                                    <span class="input-group-text" id="basic-addon1">تعداد نتایج</span>
                                    <select
                                        className='form-select'
                                        onChange={e => setPerPage(e.target.value)}>
                                        <option>
                                            8
                                        </option>
                                        <option>
                                            16
                                        </option>
                                        <option>
                                            24
                                        </option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="row">

                        <aside className="col-lg-3 col-md-4 col-sm-12 col-xs-12">
                            <section className="aside-section filter-by-category">
                                <form action="/series">
                                    <header>
                                        <h3>
                                            ژانرها
                                        </h3>
                                    </header>
                                    <div className="input-group">
                                        <ul className='mx-3'>
                                            <li className='form-check'>
                                                <input
                                                    className='form-check-input'
                                                    type="radio"
                                                    name="genre"
                                                    value="همه"
                                                    defaultChecked={"همه"}
                                                    onClick={e => {
                                                        setGENRE(undefined);
                                                        dispatch(getAllSeries(ST, undefined, sort, director, actor, channel));
                                                    }} />
                                                <label htmlFor="genre" className='form-check-label'>
                                                    همه
                                                </label>
                                            </li>
                                            {genres.map(genre => (
                                                <li className='form-check'>
                                                    <input
                                                        className='form-check-input'
                                                        type="radio"
                                                        name="genre"
                                                        id={genre._id}
                                                        key={genre._id}
                                                        value={genre.title}
                                                        onClick={e => {
                                                            setGENRE(e.target.value);
                                                            dispatch(getAllSeries(ST, e.target.value, sort, director, actor, channel));
                                                        }} />
                                                    <label htmlFor={genre} className='form-check-label'>
                                                        {genre.title}
                                                    </label>
                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                </form>
                            </section>

                            <section className="aside-section filter-by-category">
                                <form action="/series">
                                    <header>
                                        <h3>
                                            شبکه ها و پلتفرم ها
                                        </h3>
                                    </header>
                                    <div className="input-group">
                                        <ul className='mx-3'>
                                            <li className='form-check'>
                                                <input
                                                    className='form-check-input'
                                                    type="radio"
                                                    name="channel"
                                                    value="همه"
                                                    defaultChecked={"همه"}
                                                    onClick={e => {
                                                        setChannel(undefined);
                                                        dispatch(getAllSeries(ST, GENRE, sort, director, actor, undefined));
                                                    }} />
                                                <label htmlFor="channel" className='form-check-label'>
                                                    همه
                                                </label>
                                            </li>
                                            {chnls.map(channel => (
                                                <li className='form-check'>
                                                    <input
                                                        className='form-check-input'
                                                        type="radio"
                                                        name="channel"
                                                        id={channel._id}
                                                        key={channel._id}
                                                        value={channel.title}
                                                        onClick={e => {
                                                            setChannel(e.target.value);
                                                            dispatch(getAllSeries(ST, GENRE, sort, director, actor, e.target.value));
                                                        }} />
                                                    <label htmlFor={channel} className='form-check-label'>
                                                        {channel.title}
                                                    </label>
                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                </form>
                            </section>

                            <section className="aside-section filter-by-category">
                                <form action="/series">
                                    <header>
                                        <h3>
                                            کارگردانان
                                        </h3>
                                    </header>
                                    <div className="input-group">
                                        <ul className='mx-3'>
                                            <li className='form-check'>
                                                <input
                                                    className='form-check-input'
                                                    type="radio"
                                                    name="director"
                                                    value="همه"
                                                    defaultChecked={"همه"}
                                                    onClick={e => {
                                                        setDirector(undefined);
                                                        dispatch(getAllSeries(ST, GENRE, sort, undefined, actor, channel));
                                                    }} />
                                                <label htmlFor="director" className='form-check-label'>
                                                    همه
                                                </label>
                                            </li>
                                            {directors.map(director => (
                                                <li className='form-check'>
                                                    <input
                                                        className='form-check-input'
                                                        type="radio"
                                                        name="director"
                                                        id={director.fullname}
                                                        key={director._id}
                                                        value={director.fullname}
                                                        onClick={e => {
                                                            setDirector(e.target.value);
                                                            dispatch(getAllSeries(ST, GENRE, sort, e.target.value, actor, channel));
                                                        }} />
                                                    <label htmlFor="director" className='form-check-label'>
                                                        {director.fullname}
                                                    </label>
                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                </form>
                            </section>

                            <section className="aside-section filter-by-category">
                                <form action="/series">
                                    <header>
                                        <h3>
                                            بازیگران
                                        </h3>
                                    </header>
                                    <div className="input-group">
                                        <ul className='mx-3'>
                                            <li className='form-check'>
                                                <input
                                                    className='form-check-input'
                                                    type="radio"
                                                    name="actor"
                                                    value="همه"
                                                    defaultChecked={"همه"}
                                                    onClick={e => {
                                                        setActor(undefined);
                                                        dispatch(getAllSeries(ST, GENRE, sort, director, undefined, channel));
                                                    }} />
                                                <label htmlFor="actor" className='form-check-label'>
                                                    همه
                                                </label>
                                            </li>
                                            {actors.map(actor => (
                                                <li className='form-check'>
                                                    <input
                                                        className='form-check-input'
                                                        type="radio"
                                                        name="actor"
                                                        id={actor.fullname}
                                                        key={actor._id}
                                                        value={actor.fullname}
                                                        onClick={e => {
                                                            setActor(e.target.value);
                                                            dispatch(getAllSeries(ST, GENRE, sort, director, e.target.value, channel));
                                                        }} />
                                                    <label htmlFor="actor" className='form-check-label'>
                                                        {actor.fullname}
                                                    </label>
                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                </form>
                            </section>

                        </aside>

                        <div className="col-lg-9 col-md-8 col-sm-12 col-xs-12">

                            <section className="terms-items">
                                <div className="row">
                                    {indexedSeries.length > 0
                                        ? indexedSeries.map(serial => <SerialCard serial={serial} key={serial._id} />)
                                        : (
                                            <div className="col-xs-12 term-col">
                                                <div className="alert alert-info">
                                                    <p>سريالي برای نمایش وجود ندارد !</p>
                                                </div>
                                            </div>
                                        )}
                                </div>

                                {indexedSeries.length > 0
                                    ? (<Pagination
                                        dataLength={series.length}
                                        currentPage={currentPage}
                                        perPage={perPage}
                                        onPageChange={handlePageChange} />)
                                    : (null)}
                                <br />

                            </section>

                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default Series;