import { toast } from "material-react-toastify";
import { getLimit } from "../Services/ageLimitService";
import { getMovieLimit } from "../Services/movieService";
import { getSerialLimit } from "../Services/serialService";

export const getSingleLimit = limitId => {
    return async dispatch => {
        try {
            const { data, status } = await getLimit(limitId);

            if (status === 200) {
                await dispatch({ type: "GET_LIMIT", payload: data.limit });
            } else if (status === 404) {
                toast.error("محدودیت مورد نظر پیدا نشد !");
            }
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری محدودیت با مشکل مواجه شد !");
        }
    }
}

export const getSingleMovieLimit = movieId => {
    return async dispatch => {
        try {
            const { data } = await getMovieLimit(movieId);
            await dispatch({ type: "GET_LIMIT", payload: data.limit });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری محدودیت با مشکل مواجه شد !");
        }
    }
}

