import React from 'react'
import config from '../../Services/config.json';
import { formatDate } from '../../utils/formatDate';

const DirectorCard = ({ director }) => {
    return (
        <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12 term-col">
            <article className='movie-card'>
                <a href={`/directors/${director._id}`} className="img-layer"><img src={`${config.localapi}/portraits/${director.portrait}`} /></a>
                <h2>
                    <a href={`/directors/${director._id}`}>
                        {director.fullname}
                    </a>
                </h2>
                <span>
                    yrs {new Date(Date.now()).getFullYear() - formatDate(director.birthDate).getFullYear()}
                </span>
                <i>Director</i>
            </article>
        </div>
    );
}

export default DirectorCard;