import React from 'react'
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { deleteSerial } from '../../../actions/Series';
import { idValidator } from '../../../utils/idValidator';

const DeleteSerial = ({ match, history }) => {
    const dispatch = useDispatch();
    const serialId = match.params.id;

    useEffect(() => {
        if (idValidator(serialId)) {
            if (confirm("آیا از حذف کردن این سریال مطئنید ؟")) {
                dispatch(deleteSerial(serialId));
                history.replace("/admin/series");
            }
            else {
                history.replace("/admin/series");
            }
        } else {
            return location.replace("/admin/series");
        }
    }, []);

    return null;
}

export default withRouter(DeleteSerial);