import { toast } from "material-react-toastify";
import { getComments, newComment, removeComment } from "../Services/commentService"

export const getRelatedComments = movieId => {
    return async dispatch => {
        const { data } = await getComments(movieId);
        await dispatch({ type: "GET_COMMENTS", payload: data.comments });
    }
}

export const addComment = (comment, movieId) => {
    return async(dispatch, getState) => {
        const comments = [...getState().comments];

        try {
            const { data, status } = await newComment(comment, movieId);

            if (status === 201) {
                await dispatch({ type: "ADD_COMMENT", payload: data.comments });
                toast.success("کامنت با موفقیت درج شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_COMMENT", payload: comments });
            toast.error("درج کامنت شما با مشکل مواجه شد.");
        }
    }
}

export const deleteComment = commentId => {
    return async(dispatch, getState) => {
        const comments = [...getState().comments];

        try {
            const { data, status } = await removeComment(commentId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_COMMENT", payload: data.comments });
                toast.success("کامنت مورد نظر با موفقیت پاک شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "REMOVE_COMMENT", payload: comments });
            toast.error("عملیات پاکسازی کامنت موردنظر با مشکل مواجه شد!");
        }
    }
}