import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Users
export const getUsers = searchText => {
    return http.get(`${local}/admin/users?search=${searchText}`);
}

// Gets Single User
export const getUser = userId => {
    return http.get(`${local}/admin/users/${userId}`);
}

// Update User (Admin)
export const updateUser = (user, userId) => {
    return http.put(`${local}/admin/users/edit-user/${userId}`, user);
}

// Remove User (Admin)
export const removeUser = userId => {
    return http.delete(`${local}/admin/users/remove-user/${userId}`);
}

// Change User's Role
export const changeRole = user => {
    return http.put(`${local}/admin/users/change-role/${user.userId}`, JSON.stringify(user), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}