import React, { Fragment } from 'react';
import { useEffect } from 'react';
import { withRouter } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import config from '../../../Services/config.json';
import { getRelatedFiles } from '../../../actions/Files';
import { getSingleEpisode } from '../../../actions/Episode';
import { addSerialDownload } from '../../../actions/Serial';
import { Helmet } from 'react-helmet';
import { padNumber } from '../../../utils/padNumbers';

const DownloadPage = ({ match }) => {
    const dispatch = useDispatch();
    const episodeId = match.params.id;

    const episode = useSelector(state => state.episode);
    const files = useSelector(state => state.files);

    const UHD = files.filter(f => f.quality === "4k");
    const FHD = files.filter(f => f.quality === "1080p");
    const HD = files.filter(f => f.quality === "720p");
    const SD = files.filter(f => f.quality === "480p");

    useEffect(() => {
        dispatch(getRelatedFiles(episodeId));
        dispatch(getSingleEpisode(episodeId));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | فایل های قسمت
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href={`/series/episode/${episodeId}`}>فایل های قسمت {padNumber(episode.epNumber)}
                                    - {episode.title}</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <div className="col-md-12 col-sm-12 col-xs-12 ">
                            <section className="term-description">
                                <h2>فایل های قسمت {padNumber(episode.epNumber)}
                                    - {episode.title}</h2>
                                <hr /> {files.length > 0
                                    ? (
                                        <Fragment>
                                            <div>
                                                <h3>4K :</h3>
                                                {UHD.length > 0
                                                    ? UHD.map(file => (
                                                        <a
                                                            className='episode-item'
                                                            key={file._id}
                                                            href={`${config.localapi}/files/${file.fileName}`}
                                                            onClick={() => dispatch(addSerialDownload(episode.serial))}>
                                                            {file.showName}
                                                        </a>
                                                    ))
                                                    : (
                                                        <div className="alert alert-info">
                                                            <p>فایلی جهت نمایش وجود ندارد !</p>
                                                        </div>
                                                    )}
                                            </div>
                                            <hr />

                                            <div>
                                                <h3>1080p :</h3>
                                                {FHD.length > 0
                                                    ? FHD.map(file => (
                                                        <a
                                                            className='episode-item'
                                                            key={file._id}
                                                            href={`${config.localapi}/files/${file.fileName}`}
                                                            onClick={() => dispatch(addSerialDownload(episode.serial))}>
                                                            {file.showName}
                                                        </a>
                                                    ))
                                                    : (
                                                        <div className="alert alert-info">
                                                            <p>فایلی جهت نمایش وجود ندارد !</p>
                                                        </div>
                                                    )}
                                            </div>
                                            <hr />

                                            <div>
                                                <h3>720p :</h3>
                                                {HD.length > 0
                                                    ? HD.map(file => (
                                                        <a
                                                            className='episode-item'
                                                            key={file._id}
                                                            href={`${config.localapi}/files/${file.fileName}`}
                                                            onClick={() => dispatch(addSerialDownload(episode.serial))}>
                                                            {file.showName}
                                                        </a>
                                                    ))
                                                    : (
                                                        <div className="alert alert-info">
                                                            <p>فایلی جهت نمایش وجود ندارد !</p>
                                                        </div>
                                                    )}
                                            </div>
                                            <hr />

                                            <div>
                                                <h3>480p :</h3>
                                                {SD.length > 0
                                                    ? SD.map(file => (
                                                        <a
                                                            className='episode-item'
                                                            key={file._id}
                                                            href={`${config.localapi}/files/${file.fileName}`}
                                                            onClick={() => dispatch(addSerialDownload(episode.serial))}>
                                                            {file.showName}
                                                        </a>
                                                    ))
                                                    : (
                                                        <div className="alert alert-info">
                                                            <p>فایلی جهت نمایش وجود ندارد !</p>
                                                        </div>
                                                    )}
                                            </div>
                                            <hr />
                                        </Fragment>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>فایلی جهت نمایش وجود ندارد</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(DownloadPage);