import React from 'react'
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { deleteChannel } from '../../../actions/Channels';
import { idValidator } from '../../../utils/idValidator';

const DeleteChannel = ({ history, match }) => {
    const dispatch = useDispatch();
    const channelId = match.params.id;

    useEffect(() => {
        if (idValidator(channelId)) {
            if (confirm("آیا از حذف کردن این شبکه مطئنید ؟")) {
                dispatch(deleteChannel(channelId));
                history.replace("/admin/channels");
            }
            else {
                history.replace("/admin/channels");
            }
        } else {
            return location.replace("/admin/channels");
        }
    }, []);

    return null;
}

export default withRouter(DeleteChannel);