export const genresReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_GENRES":
            return [...action.payload];
        case "ADD_GENRE":
            return [...action.payload];
        case "EDIT_GENRE":
            return [...action.payload];
        case "DELETE_GENRE":
            return [...action.payload];
        default:
            return state;
    }
}