import { toast } from "material-react-toastify";
import { editDirector, getDirectors, newDirector, removeDirector } from "../Services/directorService"

export const getAllDirectors = searchText => {
    return async dispatch => {
        const { data } = await getDirectors(searchText);
        await dispatch({ type: "GET_DIRECTORS", payload: data.directors });
    }
}

export const addDirector = director => {
    return async(dispatch, getState) => {
        const directors = [...getState().directors];

        try {
            const { data, status } = await newDirector(director);

            if (status === 201) {
                await dispatch({ type: "ADD_DIRECTOR", payload: data.directors });
                toast.success("کارگردان با موفقیت اضافه شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_DIRECTOR", payload: directors });
            toast.error("عملیات اضافه کردن کارگردان با مشکل مواجه شد!");
        }
    }
}

export const updateDirector = (director, directorId) => {
    return async(dispatch, getState) => {
        const directors = [...getState().directors];

        try {
            const { data, status } = await editDirector(director, directorId);

            if (status === 200) {
                await dispatch({ type: "UPDATE_DIRECTOR", payload: data.directors });
                toast.success("کارگردان مورد نظر با موفقیت ویرایش شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "UPDATE_DIRECTOR", payload: directors });
            toast.error("عملیات ویرایش کارگردان با مشکل مواجه شد!");
        }
    }
}

export const deleteDirector = directorId => {
    return async(dispatch, getState) => {
        const directors = [...getState().directors];

        try {
            const { data, status } = await removeDirector(directorId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_DIRECTOR", payload: directors });
                toast.success("کارگردان مورد نظر با موفقیت پاک شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "REMOVE_DIRECTOR", payload: data.directors });
            toast.error("عملیات پاکسازی کارگردان با مشکل مواجه شد!");
        }
    }
}