import React, { Fragment, useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { getAllUsers } from '../../../actions/Users';
import { convertToShamsi } from '../../../utils/shamsi';
import qs from 'qs';

const UsersTable = () => {
    const dispatch = useDispatch();

    const USER = useSelector(state => state.user);
    const users = useSelector(state => state.users);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllUsers(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | کاربران</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        کاربران

                        {USER.role === "owner"
                            ? (
                                <a href={`/admin/users/change-roles`} className='btn btn-success'>
                                    ویرایش نقش کاربران
                                </a>
                            )
                            : (null)}
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>

                                {users.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>نام</th>
                                                    <th>تاریخ ثبت نام</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {users.map(user => (
                                                    <tr key={user._id}>
                                                        <td>
                                                            {user.fullname}
                                                            {user.role === "admin"
                                                                ? (
                                                                    <span
                                                                        className='badge'
                                                                        style={{
                                                                            marginRight: "10px",
                                                                            backgroundColor: "#02909b"
                                                                        }}>مدیر</span>
                                                                )
                                                                : user.role === "owner"
                                                                    ? (
                                                                        <span
                                                                            className='badge'
                                                                            style={{
                                                                                marginRight: "10px",
                                                                                backgroundColor: "#e5c102"
                                                                            }}>رئیس</span>
                                                                    )
                                                                    : null}
                                                        </td>
                                                        <td>
                                                            {convertToShamsi(user.createdAt)}
                                                        </td>
                                                        {USER.role === "owner" || user.role === "user" || USER._id === user._id
                                                            ? (
                                                                <td>
                                                                    <a
                                                                        href={`/admin/users/edit-user/${user._id}`}
                                                                        className='btn btn-warning'
                                                                        style={{
                                                                            marginLeft: "10px"
                                                                        }}>
                                                                        ویرایش
                                                                    </a>
                                                                    <a href={`/admin/users/delete-user/${user._id}`} className='btn btn-danger'>
                                                                        حذف
                                                                    </a>
                                                                </td>
                                                            )
                                                            : null}
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>کاربری جهت نمایش وجو ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>

                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default UsersTable;