import { toast } from "material-react-toastify";
import { answerMessage, getMessages, removeMessage } from "../Services/messageService";

export const getAllMessages = () => {
    return async dispatch => {
        try {
            const { data } = await getMessages();
            await dispatch({ type: "GET_MESSAGES", payload: data.messages });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاري پيام ها با مشكل مواجه شد!");
        }
    }
}

export const answerToMessage = (messageId, answer) => {
    return async dispatch => {
        try {
            const { data, status } = await answerMessage(messageId, answer);

            if (status === 200) {
                await dispatch({ type: "ANSWER_MESSAGE", payload: data.messages });
                toast.success("پاسخدهي به پيام با موفقيت انجام شد");
            }
        } catch (err) {
            console.log(err);
            toast.error("پاسخدهي به پيام با مشكل مواجه شد!");
        }
    }
}

export const deleteMessage = messageId => {
    return async(dispatch, getState) => {
        const messages = [...getState().messages];

        try {
            const { data, status } = await removeMessage(messageId);

            if (status === 200) {
                await dispatch({ type: "DELETE_MESSAGE", payload: data.messages });
                toast.success(" پاکسازی پيام با موفقيت انجام شد");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "DELETE_MESSAGE", payload: messages });
            toast.error(" پاکسازی پيام با مشكل مواجه شد!");
        }
    }
}