import React from 'react'
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { deleteEpisode } from '../../../actions/Episodes';
import { idValidator } from '../../../utils/idValidator';

const DeleteEpisode = ({ match, history }) => {
    const dispatch = useDispatch();
    const episodeId = match.params.id;

    useEffect(() => {
        if (idValidator(episodeId)) {
            if (confirm("آیا از حذف کردن این قسمت مطئنید ؟")) {
                dispatch(deleteEpisode(episodeId));
                history.goBack();
            }
            else {
                history.goBack();
            }
        } else {
            return history.goBack();
        }
    }, []);

    return null;
}

export default withRouter(DeleteEpisode);