import React, { useEffect, useState } from 'react'
import { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { getAllUsers } from '../../../actions/Users';
import { convertToShamsi } from '../../../utils/shamsi';
import qs from 'qs';

const AdminsTable = () => {
    const dispatch = useDispatch();

    const user = useSelector(state => state.user);
    const users = useSelector(state => state.users);
    const admins = users.filter(user => user.role === "admin");

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllUsers(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | مدیران</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        مدیران

                        {user.role === "owner"
                            ? (
                                <a href={`/admin/users/change-roles`} className='btn btn-success'>
                                    ویرایش نقش کاربران
                                </a>
                            )
                            : (null)}
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>

                                {admins.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>نام</th>
                                                    <th>تاریخ ثبت نام</th>
                                                    {user.role === "owner"
                                                        ? (
                                                            <th>مدیریت</th>
                                                        )
                                                        : null}
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {admins.map(admin => (
                                                    <tr key={admin._id}>
                                                        <td>
                                                            {admin.fullname}
                                                        </td>
                                                        <td>{convertToShamsi(admin.createdAt)}</td>
                                                        {user.role === "owner"
                                                            ? (
                                                                <td>
                                                                    <a
                                                                        href={`/admin/users/edit-user/${admin._id}`}
                                                                        className='btn btn-warning'
                                                                        style={{
                                                                            marginLeft: "10px"
                                                                        }}>
                                                                        ویرایش
                                                                    </a>
                                                                    <a href={`/admin/users/delete-user/${admin._id}`} className='btn btn-danger'>
                                                                        حذف کاربر
                                                                    </a>
                                                                </td>
                                                            )
                                                            : null}
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>مدیری جهت نمایش وجو ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default AdminsTable;