import React from 'react'

const MainMenu = () => {
    return (
        <nav class="navbar navbar-expand-lg bg-dark user-menu">
            <div class="container-fluid">
                <button
                    class="navbar-toggler"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav me-auto mb-2 mb-lg-0 menu-items">
                        <li className='nav-item'>
                            <a className='nav-link' href="/movies">
                                فیلم ها
                            </a>
                        </li>

                        <li className='nav-item'>
                            <a className='nav-link' href="/series">
                                سریال ها
                            </a>
                        </li>

                        <li className='nav-item'>
                            <a className='nav-link' href="/genres">
                                ژانر ها
                            </a>
                        </li>

                        <li className='nav-item'>
                            <a className='nav-link' href="/directors">
                                کارگردانان
                            </a>
                        </li>

                        <li className='nav-item'>
                            <a className='nav-link' href="/actors">
                                بازیگران
                            </a>
                        </li>

                        <li className='nav-item'>
                            <a className='nav-link' href="/channels">
                                شبکه و پلتفرم ها
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    );
}

export default MainMenu;