import React, { Fragment } from 'react'
import { Helmet } from 'react-helmet';
import { useSelector } from 'react-redux';

const AdminPage = () => {
    const user = useSelector(state => state.user);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | پنل مدیریت
                </title>
            </Helmet>
            <center>
                <p style={{
                    fontSize: "40px"
                }}>مووی لند
                    <span style={{
                        color: "#86181C"
                    }}>|</span>
                    پنل مدیریت</p>
                <hr />
                <ul className='admin-operations row'>
                    {user.role === "owner"
                        ? (
                            <li className='col-xs-12 col-sm-4 col-lg-4'>
                                <a href="/admin/users/change-roles" className='btn-add-manager'>ویرایش نقش کاربران</a>
                            </li>
                        )
                        : null}
                    <li className='col-xs-12 col-sm-4 col-lg-4'>
                        <a href="/admin/movies/add-movie" className='btn-add-book'>فیلم جدید</a>
                    </li>

                    <li className='col-xs-12 col-sm-4 col-lg-4'>
                        <a href="/admin/series/add-serial" className='btn-add-book'>سریال جدید</a>
                    </li>

                    <li className='col-xs-12 col-sm-4 col-lg-4'>
                        <a href="/admin/directors/add-director" className='btn-add-author'>کارگردان جدید</a>
                    </li>

                    <li className='col-xs-12 col-sm-4 col-lg-4'>
                        <a href="/admin/actors/add-actor" className='btn-add-author'>بازیگر جدید</a>
                    </li>

                    <li className='col-xs-12 col-sm-4 col-lg-4'>
                        <a href="/admin/genres/add-genre" className='btn-add-author'>ژانر جدید</a>
                    </li>

                    <li className='col-xs-12 col-sm-4 col-lg-4'>
                        <a href="/admin/channels/add-channel" className='btn-add-author'>شبکه جدید</a>
                    </li>

                    <li className='col-xs-12 col-sm-4 col-lg-4'>
                        <a href="/admin/age-limits/add-limit" className='btn-add-author'>محدودیت جدید</a>
                    </li>
                </ul>
            </center>
        </Fragment>
    );
}

export default AdminPage;