import React, { Fragment } from 'react'
import { Helmet } from 'react-helmet';
import { useSelector } from 'react-redux';
import { Route, Switch, withRouter } from 'react-router';
import EditProfile from './EditProfile';
import UserInfo from './UserInfo';
import config from '../../Services/config.json';
import { Link } from 'react-router-dom';
import { convertToShamsi } from '../../utils/shamsi';

const UserProfile = () => {
    const user = useSelector(state => state.user);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | پروفایل
                </title>
            </Helmet>
            <main>
                <div className="container">
                    <div className="user-account">
                        <div className="row">
                            <div className="col-md-3 col-sm-4 col-12">
                                <aside>

                                    <div className="avatar-layer">
                                        <div className="img-layer">
                                            <Link to="/user/profile/edit-profile" className="change-image">
                                                <i className="zmdi zmdi-edit"></i>
                                            </Link>
                                            {user.avatar
                                                ? (<img src={`${config.localapi}/avatars/${user.avatar}`} />)
                                                : (<img src="/images/avatar.jpg" />)}
                                        </div>
                                        <div className="detail">
                                            <span>
                                                {user.fullname}
                                            </span>
                                            <span>
                                                عضویت : {`${convertToShamsi(user.createdAt)}`}
                                            </span>
                                        </div>
                                    </div>

                                    <section>
                                        <header>
                                            <h3>
                                                میز کار
                                            </h3>
                                        </header>
                                        <div className="inner">
                                            <ul>
                                                <li>
                                                    <a href="/user/profile">
                                                        مشاهده حساب کابری
                                                    </a>
                                                </li>
                                                {user.role !== "user" ? (
                                                    <li>
                                                        <a href="/admin">
                                                            پنل مدیریت سایت
                                                        </a>
                                                    </li>
                                                ) : null}
                                                <li>
                                                    <Link to={"/user/profile/edit-profile"}>
                                                        ویرایش حساب کابری
                                                    </Link>
                                                </li>
                                                <li>
                                                    <a href="/user/logout">
                                                        خروج از حساب کاربری
                                                    </a>
                                                </li>
                                            </ul>
                                        </div>
                                    </section>
                                </aside>
                            </div>
                            <div className="col-md-9 col-sm-8 col-xs-12">
                                <Switch>
                                    <Route path={"/user/profile"} exact render={() => <UserInfo user={user} />} />
                                    <Route path={"/user/profile/edit-profile"} render={() => <EditProfile user={user} />} />
                                </Switch>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </Fragment>
    );
}

export default UserProfile;