import React, { Fragment, useRef, useState } from 'react'
import { Helmet } from 'react-helmet';
import { forgetPassword, resetPassword } from '../../Services/userService';
import { toast } from 'material-react-toastify';
import { Redirect, withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { useSelector } from 'react-redux';
import { random } from 'lodash';
// import { decodeToken } from '../../utils/decodeToken';

const ResetPassword = ({ match, history }) => {
    const token = match.params.token;

    const user = useSelector(state => state.user);

    const [password,
        setPassword] = useState("");
    const [confirmPassword,
        setConfirmPassword] = useState("");

    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: " پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 6 کاراکتر باشد!",
            email: "ایمیل وارد شده معتبر نمی باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const handleResetPassword = async event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                if (password === confirmPassword) {
                    const { status } = await resetPassword({ password, confirmPassword }, token);

                    if (status === 200) {
                        toast.success("رمز عبور شما با موفقیت بازنشانی شد.");
                        history.push("/user/login");
                    }
                } else {
                    toast.error("رمز های عبور یکسان نمی باشند !");
                }
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 100));
            }
        } catch (err) {
            toast.error(JSON.parse(err.request.response).message);
            console.log(err);
        }
    }

    if (user.fullname)
        return <Redirect to={"/"} />

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | بازنشانی رمز عبور
                </title>
            </Helmet>

            <div className="container">
                <nav aria-label="breadcrumb">
                    <ul className="breadcrumb">
                        <li className="breadcrumb-item">
                            <a href="/">مووی لند</a>
                        </li>
                        <li className="breadcrumb-item active">
                            <a href="/user/reset-password">بازنشانی رمز عبور</a>
                        </li>
                    </ul>
                </nav>
            </div>

            <main className="client-page">

                <div className="container-content">

                    <header>
                        <h2>
                            بازنشانی رمز عبور
                        </h2>
                    </header>

                    <div className="form-layer">

                        <form onSubmit={e => handleResetPassword(e)}>

                            <div className="input-group">
                                <span className="input-group-text" id="email-address">
                                    رمز جدید
                                </span>
                                <input
                                    type="password"
                                    name='password'
                                    value={password}
                                    onChange={e => {
                                        setPassword(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("password");
                                    }}
                                    className="form-control"
                                    aria-describedby="password-address" />
                            </div>
                            {validator
                                .current
                                .message("password", password, "required|min:6")}

                            <div className="input-group">
                                <span className="input-group-text" id="email-address">
                                    تایید رمز
                                </span>
                                <input
                                    type="password"
                                    name='confirmPassword'
                                    value={confirmPassword}
                                    onChange={e => {
                                        setConfirmPassword(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("confirmPassword");
                                    }}
                                    className="form-control"
                                    aria-describedby="confirmPassword-address" />
                            </div>
                            {validator
                                .current
                                .message("confirmPassword", confirmPassword, "required|min:6")}

                            <div className="input-group">
                                <div
                                    className="g-recaptcha"
                                    data-sitekey="6LdM7HghAAAAANM-6frxXMB7CW7xm1IAnU7Af9Z9"
                                    data-theme="dark"></div>
                            </div>

                            <button className="btn btn-success">
                                بازنشانی رمز عبور
                            </button>

                        </form>
                    </div>

                </div>
            </main>
        </Fragment>
    );
}

export default withRouter(ResetPassword);