import React from 'react'
import config from '../../Services/config.json'
import { useSelector } from 'react-redux';
import { convertToShamsi } from '../../utils/shamsi';
import { formatDate } from '../../utils/formatDate';
import { isEmpty } from 'lodash';

const Comment = ({ comment }) => {
    const user = useSelector(state => state.user);

    return (
        <div className="comment-row">
            {!isEmpty(user.avatar)
                ? (<img src={`${config.localapi}/avatars/${comment.user.avatar}`} />)
                : (<img src="/images/avatar.jpg" />)}
            <div className="left-col">
                <h3>
                    {comment.user.fullname}
                    {user.role !== "user" || user._id === comment.user._id
                        ? (
                            <a
                                href={`/admin/comments/delete-comment/${comment._id}`}
                                className={"btn btn-sm btn-danger btn-remove-comment"}>حذف نظر</a>
                        )
                        : (null)}
                </h3>
                <span>{`${convertToShamsi(comment.createdAt)} - ${formatDate(comment.createdAt).getHours()}:${formatDate(comment.createdAt).getMinutes()}`}</span>
                <p>
                    {comment.body}
                </p>
            </div>
        </div>
    );
}

export default Comment;