import config from './config.json';
import http from './httpService';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL


// Gets All Related Comments
export const getComments = movieId => {
    return http.get(`${local}/comments/${movieId}`);
}

// Add New Comment
export const newComment = (comment, movieId) => {
    return http.post(`${local}/comments/add-comment/${movieId}`, JSON.stringify(comment));
}

// Remove a Comment
export const removeComment = commentId => {
    return http.delete(`${local}/comments/remove-comment/${commentId}`);
}