import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Actors
export const getActors = searchText => {
    return http.get(`${local}/actors?search=${searchText}`)
}

// Gets Single Actor
export const getActor = actorId => {
    return http.get(`${local}/actors/${actorId}`);
}

// Add Actor
export const newActor = actor => {
    return http.post(`${local}/admin/actors/add-actor`, actor, {
        headers: {
            "Content-Type": "multipart/form-data"
        }
    });
}

// Edit Actor
export const editActor = (actor, actorId) => {
    return http.put(`${local}/admin/actors/edit-actor/${actorId}`, actor);
}

// Remove Actor
export const removeActor = actorId => {
    return http.delete(`${local}/admin/actors/remove-actor/${actorId}`);
}