import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getSingleMovieDirector, getSingleSerialDirector } from '../../actions/Director';
import config from '../../Services/config.json';

const DirectorInfo = ({ movieId }) => {
    const dispatch = useDispatch();
    const director = useSelector(state => state.director);

    useEffect(() => {
        if (location.pathname.includes("series")) {
            dispatch(getSingleSerialDirector(movieId));
        } else if (location.pathname.includes("movies")) {
            dispatch(getSingleMovieDirector(movieId));
        }
    }, []);

    return (
        <article className="teacher-info">
            <img src={`${config.localapi}/portraits/${director.portrait}`} />
            <h2>
                کارگردان : {" "}
                <a href={`/directors/${director._id}`}>{director.fullname}</a>
            </h2>
            <p>
                {director.shortInfo}
            </p>
        </article>
    );
}

export default DirectorInfo;