import React, { useEffect } from 'react'
import { formatDate } from '../../utils/formatDate';
import { useDispatch, useSelector } from 'react-redux';
import { getSingleSerialGenre } from '../../actions/Genre';
import { getSingleSerialChannel } from '../../actions/Channel';
import { getSingleSerialLimit } from '../../actions/AgeLimit';

const SerialInfo = ({ serial, serialId }) => {
    const dispatch = useDispatch();

    const genre = useSelector(state => state.genre);
    const channel = useSelector(state => state.channel);
    const ageRestriction = useSelector(state => state.limit);

    useEffect(() => {
        dispatch(getSingleSerialGenre(serialId));
        dispatch(getSingleSerialChannel(serialId));
        dispatch(getSingleSerialLimit(serialId));
    }, []);

    return (
        <article className="term-info">
            <h2>
                اطلاعات فیلم
            </h2>
            <ul>
                <li>ژانر : {genre.title}</li>
                <li>محدودیت سنی : {ageRestriction.title}</li>
                <li>شبکه : {channel.title}</li>
                <li>نمره ی IMDB : {serial.imdbRating}</li>
                <li>تاریخ انتشار : {formatDate(serial.datePublished).getFullYear()}</li>
                <li>تعداد فصول : فصل {serial.seasons}</li>
                <li>تعداد دانلود ها : {serial.downloads}</li>
            </ul>
        </article>
    );
}

export default SerialInfo;