import React, { useEffect, useState } from 'react'
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { convertToShamsi } from '../../../utils/shamsi';
import config from '../../../Services/config.json';
import { getAllMovies } from '../../../actions/Movies';
import qs from 'qs';

const MoviesTable = ({ location }) => {
    const dispatch = useDispatch();

    const movies = useSelector(state => state.movies);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllMovies(searchText, undefined));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | فیلم ها</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        فیلم ها

                        <a href={`/admin/movies/add-movie`} className='btn btn-success'>
                            فیلم جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {movies.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>فایل ها</th>
                                                    <th>کاور</th>
                                                    <th>تاریخ عرضه</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {movies.map(movie => (
                                                    <tr key={movie._id}>
                                                        <td>
                                                            <a target={'_blank'} href={`/movies/${movie._id}`}>{movie.title}</a>
                                                        </td>
                                                        <td>
                                                            <a target={'_blank'} href={`/admin/files/${movie._id}`}>نمایش فایل ها</a>
                                                        </td>
                                                        <td>
                                                            <a target={'_blank'} href={`${config.localapi}/thumbnails/${movie.thumbnail}`}>{movie.thumbnail}</a>
                                                        </td>
                                                        <td>{convertToShamsi(movie.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/movies/edit-movie/${movie._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a href={`/admin/movies/delete-movie/${movie._id}`} className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>فیلمی جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default MoviesTable;