import React, { useRef, useState } from 'react';
import Comment from './Comment';
import Pagination from '../common/Pagination';
import { useDispatch, useSelector } from 'react-redux';
import SimpleReactValidator from 'simple-react-validator';
import { addComment } from '../../actions/Comments';
import { paginate } from '../../utils/paginate';

const Comments = ({ comments, movieId }) => {
    const user = useSelector(state => state.user);

    const [comment,
        setComment] = useState("");
    const [currentPage,
        setCurrentPage] = useState(1);
    const [perPage,
        setPerPage] = useState(8);
    const [,
        forceUpdate] = useState(0);

    const dispatch = useDispatch();

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: " پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 5 کاراکتر باشد!",
            email: "ایمیل وارد شده معتبر نمی باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const handlePageChange = page => {
        setCurrentPage(page);
    }

    const handleAddComment = async event => {
        event.preventDefault();

        const CMNT = {
            comment
        };

        try {
            if (validator.current.allValid()) {
                dispatch(addComment(CMNT, movieId));
                location.reload();
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 100));
            }
        } catch (err) {
            toast.error("درج نظر با مشکل مواجه شد!");
            console.log(err);
        }
    }

    const indexedComments = paginate(comments, currentPage, perPage);

    return (
        <section className="user-comments">
            <header>
                <h3>
                    نظرات کاربران
                </h3>
            </header>
            <div className="inner">

                {user.fullname
                    ? (
                        <form className='row g-3' onSubmit={e => handleAddComment(e)}>
                            <div className=" input-group">
                                <span class="input-group-text" id="basic-addon1">نظر شما</span>
                                <textarea
                                    className="form-control"
                                    placeholder="..."
                                    name='comment'
                                    rows={4}
                                    value={comment}
                                    onChange={e => {
                                        setComment(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("comment");
                                    }}></textarea>
                            </div>
                            {validator
                                .current
                                .message("comment", comment, "required")}
                            <br />

                            <div className="input-group">
                                {/* Google ReCaptcha */}
                                <div
                                    className="g-recaptcha"
                                    data-sitekey="6LdM7HghAAAAANM-6frxXMB7CW7xm1IAnU7Af9Z9"
                                    data-theme="dark"></div>
                            </div>

                            <div className="input-group">
                                <div className="col-xs-12">
                                    <button type="submit" className="btn btn-success">
                                        ثبت دیدگاه
                                    </button>
                                </div>
                            </div>
                        </form>
                    )
                    : (
                        <div className='alert alert-info'>
                            <p>جهت درج نظر بايد وارد سايت شويد</p>
                        </div>
                    )}

                <div className="comment-list">

                    {indexedComments.length !== 0
                        ? indexedComments.map(comment => (<Comment key={comment._id} comment={comment} />))
                        : (
                            <div className='alert alert-info'>
                                <p>اولين نفري باشيد كه نظر مي دهد!</p>
                            </div>
                        )
                    }

                    <br /> {indexedComments.length !== 0
                        ? (<Pagination
                            dataLength={comments.length}
                            currentPage={currentPage}
                            perPage={perPage}
                            onPageChange={handlePageChange} />)
                        : (null)}

                </div>
            </div>
        </section>
    );
}

export default Comments;