import { combineReducers } from "redux";
import { actorReducer } from "./Actor";
import { actorsReducer } from "./Actors";
import { adminUserReducer } from "./AdminUser";
import { limitReducer } from "./AgeLimit";
import { limitsReducer } from "./AgeLimits";
import { channelReducer } from "./Channel";
import { channelsReducer } from "./Channels";
import { commentsReducer } from "./Comments";
import { directorReducer } from "./Director";
import { directorsReducer } from "./Directors";
import { episodeReducer } from "./Episode";
import { episodesReducer } from "./Episodes";
import { fileReducer } from "./File";
import { filesReducer } from "./Files";
import { genreReducer } from "./Genre";
import { genresReducer } from "./Genres";
import { messageReducer } from "./Message";
import { messagesReducer } from "./Messages";
import { movieReducer } from "./Movie";
import { moviesReducer } from "./Movies";
import { serialReducer } from "./Serial";
import { seriesReducer } from "./Series";
import { userReducer } from "./User";
import { usersReducer } from "./Users";

export const reducers = combineReducers({
    movie: movieReducer,
    movies: moviesReducer,
    serial: serialReducer,
    series: seriesReducer,
    episode: episodeReducer,
    episodes: episodesReducer,
    director: directorReducer,
    directors: directorsReducer,
    actor: actorReducer,
    actors: actorsReducer,
    user: userReducer,
    users: usersReducer,
    adminUser: adminUserReducer,
    comments: commentsReducer,
    file: fileReducer,
    files: filesReducer,
    message: messageReducer,
    messages: messagesReducer,
    genre: genreReducer,
    genres: genresReducer,
    channel: channelReducer,
    channels: channelsReducer,
    limit: limitReducer,
    limits: limitsReducer
});