import React from 'react'
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { deleteActor } from '../../../actions/Actors';
import { idValidator } from '../../../utils/idValidator';

const DeleteActor = ({ match, history }) => {
    const dispatch = useDispatch();
    const actorId = match.params.id;

    useEffect(() => {
        if (idValidator(actorId)) {
            if (confirm("آیا از حذف کردن این بازیگر مطئنید ؟")) {
                dispatch(deleteActor(actorId));
                history.replace("/admin/actors");
            }
            else {
                history.replace("/admin/actors");
            }
        } else {
            return location.replace("/admin/actors");
        }
    }, []);

    return null;
}

export default withRouter(DeleteActor);