import React from 'react';
import { ToastContainer } from 'material-react-toastify';
import { BrowserRouter } from 'react-router-dom';
import MovieLand from './MovieLand';

const App = () => {
    return (
        <BrowserRouter>
            <MovieLand />
            <ToastContainer
                position="top-right"
                autoClose={5000}
                hideProgressBar
                newestOnTop={false}
                closeOnClick
                rtl={true}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />
        </BrowserRouter>
    );
}

export default App;