import { toast } from "material-react-toastify";
import { getGenre } from "../Services/genreService";
import { getMovieGenre } from "../Services/movieService";
import { getSerialGenre } from "../Services/serialService";

export const getSingleGenre = genreId => {
    return async dispatch => {
        try {
            const { data, status } = await getGenre(genreId);

            if (status === 200) {
                await dispatch({ type: "GET_GENRE", payload: data.genre });
            } else if (status === 404) {
                toast.error("ژانر مورد نظر پیدا نشد !");
            }
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری ژانر با مشکل مواجه شد !");
        }
    }
}

export const getSingleMovieGenre = movieId => {
    return async dispatch => {
        try {
            const { data } = await getMovieGenre(movieId);
            await dispatch({ type: "GET_GENRE", payload: data.genre });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری ژانر با مشکل مواجه شد !");
        }
    }
}

export const getSingleSerialGenre = serialId => {
    return async dispatch => {
        try {
            const { data } = await getSerialGenre(serialId);
            await dispatch({ type: "GET_GENRE", payload: data.genre });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری ژانر با مشکل مواجه شد !");
        }
    }
}