import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { reactionToMovie } from '../../actions/Movie';

const Rating = ({ movieId }) => {
    const dispatch = useDispatch();
    const user = useSelector(state => state.user);

    const handleReaction = (event, method) => {
        event.preventDefault();

        if (method === "like") {
            if (!user.likedMovies.includes(movieId)) {
                dispatch(reactionToMovie(movieId, { method }));
                location.reload();
            }
        } else if (method === "dislike") {
            if (!user.dislikedMovies.includes(movieId)) {
                dispatch(reactionToMovie(movieId, { method }));
                location.reload();
            }
        }
    }

    return (
        <div className="statistics">
            <h2>
                نظر سنجی
            </h2>
            <hr />

            <a
                className='btn-reaction btn-like'
                onClick={e => handleReaction(e, "like")}>
                دوست داشتم
            </a>

            <a
                className='btn-reaction btn-dislike'
                onClick={e => handleReaction(e, "dislike")}>
                دوست نداشتم
            </a>
        </div>
    );
}

export default Rating;