export const channelsReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_CHANNELS":
            return [...action.payload];
        case "ADD_CHANNEL":
            return [...action.payload];
        case "EDIT_CHANNEL":
            return [...action.payload];
        case "DELETE_CHANNEL":
            return [...action.payload];
        default:
            return state;
    }
}