import React from 'react'
import config from '../../Services/config.json';
import { formatDate } from '../../utils/formatDate';

const MovieCard = ({ movie }) => {
    return (
        <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12 term-col">
            <article className='movie-card'>
                <a href={`/movies/${movie._id}`} className="img-layer"><img src={`${config.localapi}/thumbnails/${movie.thumbnail}`} /></a>
                <h2>
                    <a href={`/movies/${movie._id}`}>
                        {movie.title}
                    </a>
                </h2>
                <span>
                    <a className='director-link' href={`/directors/${movie.director._id}`}>
                        {movie.director.fullname}
                    </a>
                </span>
                <i>{formatDate(movie.datePublished).getFullYear()}</i>
            </article>
        </div>
    );
}

export default MovieCard;