import { toast } from "material-react-toastify";
import { editChannel, getChannels, newChannel, removeChannel } from "../Services/channelService";

export const getAllChannels = searchText => {
    return async dispatch => {
        try {
            const { data } = await getChannels(searchText);
            await dispatch({ type: "GET_CHANNELS", payload: data.channels });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری شبکه ها با مشکل مواجه شد !");
        }
    }
}

export const addChannel = channel => {
    return async dispatch => {
        try {
            const { data, status } = await newChannel(channel);

            if (status === 201) {
                await dispatch({ type: "ADD_CHANNEL", payload: data.channels });
                toast.success("شبکه مورد نظر با موفقیت اضافه شد !");
            }
        } catch (err) {
            console.log(err);
            toast.error("عملیات اضافه کردن شبکه با مشکل مواجه شد !");
        }
    }
}

export const updateChannel = (channelId, channel) => {
    return async(dispatch, getState) => {
        const channels = [...getState().channels];

        try {
            const { data, status } = await editChannel(channelId, channel);

            if (status === 200) {
                await dispatch({ type: "EDIT_CHANNEL", payload: data.channels });
                toast.success("ویرایش شبکه مورد نظر موفقیت آمیز بود.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "EDIT_CHANNEL", payload: channels });
            toast.error("عملیات ویرایش شبکه با مشکل مواجه شد !");
        }
    }
}

export const deleteChannel = channelId => {
    return async(dispatch, getState) => {
        const channels = [...getState().channels];

        try {
            const { data, status } = await removeChannel(channelId);

            if (status === 200) {
                await dispatch({ type: "DELETE_CHANNEL", payload: data.channels });
                toast.success("شبکه مورد نظر با موفقیت پاک شد.");
            } else if (status === 404) {
                toast.error("شبکه مورد نظر پیدا نشد !");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "DELETE_CHANNEL", payload: channels });
            toast.error("عملیات پاکسازی شبکه با مشکل مواجه شد !");
        }
    }
}