import React, { useEffect } from 'react';
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import { getRelatedFiles } from '../../../actions/Files';
import { convertToShamsi } from '../../../utils/shamsi';
import qs from 'qs';

const EpisodesTable = ({ match }) => {
    const dispatch = useDispatch();
    const movieId = match.params.id;

    const files = useSelector(state => state.files);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getRelatedFiles(movieId, searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | فايل های فیلم</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        فایل های فیلم

                        <a href={`/admin/files/add-file/${movieId}`} className='btn btn-success'>
                            فایل جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {files.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>تاریخ ساخت</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {files.map(file => (
                                                    <tr key={file._id}>
                                                        <td>
                                                            <a>{file.showName}</a>
                                                        </td>
                                                        <td>{convertToShamsi(file.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/files/edit-file/${file._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a href={`/admin/files/delete-file/${file._id}`} className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>فایلی جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(EpisodesTable);