import { range } from 'lodash';
import React from 'react'

const Pagination = ({ dataLength, currentPage, perPage, onPageChange }) => {
    const pageCount = Math.ceil(dataLength / perPage);
    if (pageCount === 1)
        return null;

    const pages = range(1, pageCount + 1);

    return (
        <nav aria-label="Page navigation example">
            <ul className="pagination justify-content-center pages-container">
                <li className="page-item">
                    <a
                        className="page-link"
                        onClick={() =>
                            currentPage !== 1 ? onPageChange(currentPage - 1) : null}
                    >قبلی</a>
                </li>
                {pages.map(page => (
                    <li
                        key={page}
                        claclassName={page === currentPage
                            ? "page-item active"
                            : "page-item"}>
                        <a className="page-link" onClick={() => onPageChange(page)}>
                            {page}
                        </a>
                    </li>
                ))}
                <li className="page-item">
                    <a className="page-link"
                        onClick={() =>
                            currentPage !== pageCount ? onPageChange(currentPage + 1) : null}
                    >بعدی</a>
                </li>
            </ul>
        </nav>
    );
}

export default Pagination;