import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Movies
export const getMovies = (searchText, genre, sort, director, actor, channel) => {
    return http.get(`${local}/movies?search=${searchText}&genre=${genre}&sort=${sort}&director=${director}&actor=${actor}&channel=${channel}`);
}

// Gets Single Movie
export const getMovie = movieId => {
    return http.get(`${local}/movies/${movieId}`);
}

// Gets Movie Director
export const getMovieDirector = movieId => {
    return http.get(`${local}/movies/movie-director/${movieId}`);
}

// Gets Movie Actors
export const getMovieActors = movieId => {
    return http.get(`${local}/movies/movie-actors/${movieId}`);
}

// Gets Movie Genre
export const getMovieGenre = movieId => {
    return http.get(`${local}/movies/movie-genre/${movieId}`);
}

// Gets Movie Channel
export const getMovieChannel = movieId => {
    return http.get(`${local}/movies/movie-channel/${movieId}`);
}

// Gets Movie Limit
export const getMovieLimit = movieId => {
    return http.get(`${local}/movies/movie-limit/${movieId}`);
}

// Add Movie
export const newMovie = movie => {
    return http.post(`${local}/admin/movies/add-movie`, movie, {
        headers: {
            "Content-Type": "multipart/form-data"
        }
    });
}

// Edit Movie
export const editMovie = (movieId, movie) => {
    return http.put(`${local}/admin/movies/edit-movie/${movieId}`, movie);
}

// React To Movie
export const reactToMovie = (movieId, method) => {
    return http.put(`${local}/movies/react-to-movie/${movieId}`, JSON.stringify(method), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}

// Remove Movies
export const addDownload = movieId => {
    return http.put(`${local}/movies/add-download/${movieId}`);
}

// Remove Movies
export const removeMovie = movieId => {
    return http.delete(`${local}/admin/movies/remove-movie/${movieId}`);
}