import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Channels
export const getChannels = searchText => {
    return http.get(`${local}/channels?search=${searchText}`)
}

// Gets Single Channel
export const getChannel = channelId => {
    return http.get(`${local}/admin/channels/${channelId}`);
}

// Add Channel
export const newChannel = channel => {
    return http.post(`${local}/admin/channels/add-channel`, JSON.stringify(channel));
}

// Edit Channel
export const editChannel = (channelId, channel) => {
    return http.put(`${local}/admin/channels/edit-channel/${channelId}`, JSON.stringify(channel), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}

// Remove Channel
export const removeChannel = channelId => {
    return http.delete(`${local}/admin/channels/remove-channel/${channelId}`);
}