import React, { Fragment } from 'react'
import { Helmet } from 'react-helmet';

const NotFound = () => {
    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | گم شدیم 😐
                </title>
            </Helmet>
            <div className='notFound'>
                <h1>4 <span style={{ color: "#E41B21", fontFamily: "Sahel" }}>0</span> 4</h1>
                <br />
                <h2>! Sorry. We couldn't find your page</h2>
                <br />
                <h2>متاسفانه صفحه مورد نظر شما پیدا نشد !</h2>
                <br />
            </div>
        </Fragment>
    );
}

export default NotFound;