export const episodesReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_EPISODES":
            return [...action.payload];
        case "ADD_EPISODE":
            return [...action.payload];
        case "UPDATE_EPISODE":
            return [...action.payload];
        case "REMOVE_EPISODE":
            return [...action.payload];
        default:
            return state;
    }
}