import { toast } from "material-react-toastify";
import { newFile, editFile, getFiles, removeFile } from "../Services/fileService"

export const getRelatedFiles = (movieId, searchText) => {
    return async dispatch => {
        try {
            const { data } = await getFiles(movieId, searchText);
            await dispatch({ type: "GET_FILES", payload: data.files });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری فایل ها با مشکل مواجه شد!");
        }
    }
}

export const addFile = (movieId, file) => {
    return async(dispatch, getState) => {
        const files = [...getState().files];

        try {
            const { data, status } = await newFile(movieId, file);

            if (status === 201) {
                await dispatch({ type: "ADD_FILE", payload: data.files });
                toast.success("فایل با موفقیت اضافه شد!");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_FILE", payload: files });
            toast.error("اضافه کردن فایل با مشکل مواجه شد!");
        }
    }
}

export const updateFile = (fileId, file) => {
    return async(dispatch, getState) => {
        const files = [...getState().files];

        try {
            const { data, status } = await editFile(fileId, file);

            if (status === 200) {
                await dispatch({ type: "UPDATE_FILE", payload: data.files });
                toast.success("فایل موردنظر با موفقیت ویرایش شد!");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_FILE", payload: files });
            toast.error("ویرایش فایل با مشکل مواجه شد!");
        }
    }
}

export const deleteFile = fileId => {
    return async(dispatch, getState) => {
        const files = [...getState().files];

        try {
            const { data, status } = await removeFile(fileId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_FILE", payload: data.files });
                toast.success("فایل موردنظر با موفقیت پاک شد!");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_FILE", payload: files });
            toast.error("پاکسازی فایل با مشکل مواجه شد!");
        }
    }
}