import React, { Fragment, useEffect } from 'react'
import { Helmet } from 'react-helmet'
import { useDispatch, useSelector } from 'react-redux';
import { getAllChannels } from '../../actions/Channels';

const Channels = () => {
    const dispatch = useDispatch();
    const channels = useSelector(state => state.channels);

    useEffect(() => {
        dispatch(getAllChannels());
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | کانال ها
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/movies/channels">كانال ها و پلتفرم ها</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <aside className="col-md-12 col-sm-12 col-xs-12">
                            <div className="statistics">
                                <h2>
                                    كانال ها و پلتفرم ها
                                </h2>
                                <hr />

                                {channels.map(channel => (
                                    <a href={`/movies?channel=${channel.title}`} key={channel._id}>
                                        {channel.title}
                                    </a>
                                ))}
                            </div>
                        </aside>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default Channels;