import { toast } from "material-react-toastify";
import { getMessage } from "../Services/messageService";

export const getSingleMessage = messageId => {
    return async dispatch => {
        try {
            const { data } = await getMessage(messageId);
            await dispatch({ type: "GET_MESSAGE", payload: data.message });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاري پيام با مشكل مواجه شد!");
        }
    }
}