import { random } from 'lodash';
import React, { Fragment, useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { getSingleEpisode } from '../../../actions/Episode';
import { updateEpisode } from '../../../actions/Episodes';

const EditEpisode = ({ match, history }) => {
    const dispatch = useDispatch();
    const episodeId = match.params.id;

    const episode = useSelector(state => state.episode);

    const [title,
        setTitle] = useState("");
    const [season,
        setSeason] = useState(0);
    const [epNumber,
        setEpNumber] = useState(0);
    const [timeLength,
        setTimeLength] = useState(0);
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد",
            min: "کمتر از 5 کاراکتر نباید باشد"
        },
        element: message => <div
            className='alert alert-danger'
            style={{
                marginTop: "5px"
            }}>
            {message}
        </div>
    }));

    const handleAddEpisode = event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                dispatch(updateEpisode(episodeId, { title, season, epNumber, timeLength }));
                history.replace(`/admin/series/episodes/${episode.serial}`);
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error(err.response.data);
        }
    }

    useEffect(() => {
        dispatch(getSingleEpisode(episodeId));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | قسمت جدید</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        قسمت جدید
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <form onSubmit={e => handleAddEpisode(e)}>
                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">عنوان</span>
                                        <input
                                            type="text"
                                            name='title'
                                            value={title}
                                            placeholder={episode.title}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setTitle(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("title");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("title", title, "required|min:4")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">فصل</span>
                                        <input
                                            type="number"
                                            min={1}
                                            name='season'
                                            value={season}
                                            placeholder={episode.season}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setSeason(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("season");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("season", season, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">شماره قسمت</span>
                                        <input
                                            type="number"
                                            min={1}
                                            name='epNumber'
                                            value={epNumber}
                                            placeholder={episode.epNumber}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setEpNumber(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("epNumber");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("epNumber", epNumber, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">مدت زمان (دقیقه)</span>
                                        <input
                                            type="number"
                                            min={0}
                                            name='timeLength'
                                            value={timeLength}
                                            placeholder={episode.timeLength}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setTimeLength(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("timeLength");
                                            }} />

                                    </div>
                                    {validator
                                        .current
                                        .message("timeLength", timeLength, "required")}
                                    <br />

                                    <button type='submit' className='btn btn-success'>
                                        تایید
                                    </button>

                                    <button
                                        className='btn btn-warning'
                                        style={{
                                            marginRight: "10px"
                                        }}
                                        onClick={() => history.push(`/admin/series/episodes/${episode.serial}`)}>
                                        انصراف
                                    </button>
                                </form>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(EditEpisode);