import React, { useEffect } from 'react';
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import { convertToShamsi } from '../../../utils/shamsi';
import qs from 'qs';
import { getRelatedEpisodes } from '../../../actions/Episodes';

const FilesTable = ({ match }) => {
    const dispatch = useDispatch();
    const serialId = match.params.id;

    const episodes = useSelector(state => state.episodes);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getRelatedEpisodes(serialId, searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | قسمت های سریال</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        قسمت های سریال

                        <a href={`/admin/series/episodes/add-episode/${serialId}`} className='btn btn-success'>
                            قسمت جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {episodes.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>فایل ها</th>
                                                    <th>تاریخ ساخت</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {episodes.map(episode => (
                                                    <tr key={episode._id}>
                                                        <td> {episode.title} </td>
                                                        <td><a href={`/admin/files/${episode._id}`}>نمایش همه</a></td>
                                                        <td>{convertToShamsi(episode.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/series/episodes/edit-episode/${episode._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a href={`/admin/series/episodes/delete-episode/${episode._id}`} className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>قسمتی جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(FilesTable);