import { toast } from "material-react-toastify";
import { editEpisode, getEpisodes, newEpisode, removeEpisode } from "../Services/episodeService";

export const getRelatedEpisodes = (serialId, searchText, season) => {
    return async dispatch => {
        try {
            const { data } = await getEpisodes(serialId, searchText, season);
            await dispatch({ type: "GET_EPISODES", payload: data.episodes });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری قسمت ها با مشکل مواجه شد!");
        }
    }
}

export const addEpisode = (serialId, episode) => {
    return async(dispatch, getState) => {
        const episodes = [...getState().episodes];

        try {
            const { data, status } = await newEpisode(serialId, episode);

            if (status === 201) {
                await dispatch({ type: "ADD_EPISODE", payload: data.episodes });
                toast.success("قسمت با موفقیت اضافه شد!");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_EPISODE", payload: episodes });
            toast.error("اضافه کردن قسمت با مشکل مواجه شد!");
        }
    }
}

export const updateEpisode = (episodeId, episode) => {
    return async(dispatch, getState) => {
        const episodes = [...getState().episodes];

        try {
            const { data, status } = await editEpisode(episodeId, episode);

            if (status === 200) {
                await dispatch({ type: "UPDATE_EPISODE", payload: data.episodes });
                toast.success("قسمت موردنظر با موفقیت ویرایش شد!");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_EPISODE", payload: episodes });
            toast.error("ویرایش قسمت با مشکل مواجه شد!");
        }
    }
}

export const deleteEpisode = episodeId => {
    return async(dispatch, getState) => {
        const episodes = [...getState().episodes];

        try {
            const { data, status } = await removeEpisode(episodeId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_EPISODE", payload: data.episodes });
                toast.success("قسمت موردنظر با موفقیت پاک شد!");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_EPISODE", payload: episodes });
            toast.error("پاکسازی قسمت با مشکل مواجه شد!");
        }
    }
}