import { toast } from "material-react-toastify";
import { getChannel } from "../Services/channelService";
import { getMovieChannel } from "../Services/movieService";
import { getSerialChannel } from "../Services/serialService";

export const getSingleChannel = channelId => {
    return async dispatch => {
        try {
            const { data, status } = await getChannel(channelId);

            if (status === 200) {
                await dispatch({ type: "GET_CHANNEL", payload: data.channel });
            } else if (status === 404) {
                toast.error("شبکه مورد نظر پیدا نشد !");
            }
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری شبکه با مشکل مواجه شد !");
        }
    }
}

export const getSingleMovieChannel = movieId => {
    return async dispatch => {
        try {
            const { data } = await getMovieChannel(movieId);
            await dispatch({ type: "GET_CHANNEL", payload: data.channel });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری شبکه با مشکل مواجه شد !");
        }
    }
}

export const getSingleSerialChannel = serialId => {
    return async dispatch => {
        try {
            const { data } = await getSerialChannel(serialId);
            await dispatch({ type: "GET_CHANNEL", payload: data.channel });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری شبکه با مشکل مواجه شد !");
        }
    }
}