import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// All Related Files
export const getFiles = (movieId, searchText) => {
    return http.get(`${local}/files/${movieId}?search=${searchText}`);
}

// Single File
export const getFile = fileId => {
    return http.get(`${local}/admin/files/single-file/${fileId}`);
}

// Add File
export const newFile = (movieId, file) => {
    return http.post(`${local}/admin/files/add-file/${movieId}`, file, {
        headers: {
            "Content-Type": "multipart/form-data"
        }
    });
}

//Edit  File
export const editFile = (fileId, file) => {
    return http.put(`${local}/admin/files/edit-file/${fileId}`, file);
}

// Remove File
export const removeFile = fileId => {
    return http.delete(`${local}/admin/files/remove-file/${fileId}`);
}