import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { useSelector } from 'react-redux';
import { convertToShamsi } from '../../../utils/shamsi';
import { getAllActors } from '../../../actions/Actors';
import config from '../../../Services/config.json';
import qs from 'qs';

const ActorsTable = () => {
    const dispatch = useDispatch();
    const actors = useSelector(state => state.actors);

    const searchText = qs.parse(location.search, { ignoreQueryPrefix: true }).search;

    useEffect(() => {
        dispatch(getAllActors(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | بازیگران</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        بازیگران

                        <a href={`/admin/actors/add-actor`} className='btn btn-success'>
                            بازیگر جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {actors.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>نام</th>
                                                    <th>تصویر</th>
                                                    <th>تاریخ اضافه شدن</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {actors.map(actor => (
                                                    <tr key={actor._id}>
                                                        <td>
                                                            <a target={'_blank'} href={`/actor/${actor._id}`}>{actor.fullname}</a>
                                                        </td>
                                                        <td>
                                                            <a target={'_blank'} href={`${config.localapi}/portraits/${actor.portrait}`}>{actor.portrait}</a>
                                                        </td>
                                                        <td>{convertToShamsi(actor.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/actors/edit-actor/${actor._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a href={`/admin/actors/delete-actor/${actor._id}`} className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>بازیگری جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default ActorsTable;