export const filesReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_FILES":
            return [...action.payload];
        case "ADD_FILE":
            return [...action.payload];
        case "UPDATE_FILE":
            return [...action.payload];
        case "REMOVE_FILE":
            return [...action.payload];
        default:
            return state;
    }
}