import React, { useEffect, useState } from 'react'
import { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { useSelector } from 'react-redux';
import { getAllDirectors } from '../../../actions/Directors';
import { convertToShamsi } from '../../../utils/shamsi';
import { useDispatch } from 'react-redux';
import config from '../../../Services/config.json';
import qs from 'qs';

const DirectorsTable = () => {
    const dispatch = useDispatch();
    const directors = useSelector(state => state.directors);

    const searchText = qs.parse(location.search, { ignoreQueryPrefix: true }).search;

    console.log(searchText);

    useEffect(() => {
        dispatch(getAllDirectors(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | کارگردانان</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        کارگردانان

                        <a href={`/admin/directors/add-director`} className='btn btn-success'>
                            کارگردان جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>

                                {directors.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>نام</th>
                                                    <th>تصویر</th>
                                                    <th>تاریخ اضافه شدن</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {directors.map(director => (
                                                    <tr key={director._id}>
                                                        <td>
                                                            <a target={'_blank'} href={`/directors/${director._id}`}>{director.fullname}</a>
                                                        </td>
                                                        <td>
                                                            <a target={'_blank'} href={`${config.localapi}/portraits/${director.portrait}`}>{director.portrait}</a>
                                                        </td>
                                                        <td>{convertToShamsi(director.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/directors/edit-director/${director._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a
                                                                href={`/admin/directors/delete-director/${director._id}`}
                                                                className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>کارگردانی جهت نمایش وجو ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default DirectorsTable;