import { random } from 'lodash';
import { toast } from 'material-react-toastify';
import React, { Fragment, useEffect, useRef, useState } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import SimpleReactValidator from 'simple-react-validator';
import { getSingleActor } from '../../../actions/Actor';
import { updateActor } from '../../../actions/Actors';
import config from '../../../Services/config.json';
import { idValidator } from '../../../utils/idValidator';

const EditActor = ({ match, history }) => {
    const dispatch = useDispatch();
    const actorId = match.params.id;

    const actor = useSelector(state => state.actor);

    const [fullname,
        setFullname] = useState(actor.fullname);
    const [shortInfo,
        setShortInfo] = useState(actor.shortInfo);
    const [birthDate,
        setBirthDate] = useState(actor.birthDate);
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد",
            min: "کمتر از 5 کاراکتر نباید باشد"
        },
        element: message => <div
            className='alert alert-danger'
            style={{
                marginTop: "5px"
            }}>
            {message}
        </div>
    }));

    const handleEditActor = event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                const actor = new FormData();
                actor.append("fullname", fullname);
                actor.append("shortInfo", shortInfo);
                actor.append("birthDate", birthDate);
                actor.append("portrait", document.getElementById("actor-portrait").files[0]);

                dispatch(updateActor(actor, actorId));
                history.replace(`/admin/actors`);
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(0, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error(JSON.parse(err.request.response).message);
        }
    }

    useEffect(() => {
        if (idValidator(actorId)) {
            dispatch(getSingleActor(actorId));
        } else {
            return location.replace("/admin/actors");
        }
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | ویرایش بازیگر</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        ویرایش بازیگر
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <form className='row g-3' onSubmit={e => handleEditActor(e)}>
                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">نام کامل</span>
                                        <input
                                            type="text"
                                            name='fullname'
                                            value={fullname}
                                            placeholder={actor.fullname}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setFullname(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("fullname");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("fullname", fullname, "required|min:4")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">توضیحات</span>
                                        <textarea
                                            name='shortInfo'
                                            value={shortInfo}
                                            placeholder={actor.shortInfo}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setShortInfo(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("shortInfo");
                                            }}></textarea>
                                    </div>
                                    {validator
                                        .current
                                        .message("shortInfo", shortInfo, "required|min:10")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">تاریخ تولد</span>
                                        <input
                                            type="date"
                                            name='birthDate'
                                            value={birthDate}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setBirthDate(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("birthDate");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("birthDate", birthDate, "required")}
                                    <br />

                                    <div className="input-group">
                                        <div className='alert alert-info'>
                                            <a target={'_blank'} href={`${config.localapi}/portraits/${actor.portrait}`}>مشاهده ي پرتره بازيگر</a>
                                        </div>
                                    </div>

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">پرتره</span>
                                        <input
                                            type="file"
                                            name='portrait'
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            id='actor-portrait' />
                                    </div>
                                    <br />

                                    <div className="input-group">
                                        <button type='submit' className='btn btn-success col-md-3'>
                                            تایید
                                        </button>
                                        <button
                                            className='btn btn-warning col-md-3'
                                            style={{
                                                marginRight: "10px"
                                            }}
                                            onClick={() => history.push(`/admin/actors`)}>
                                            انصراف
                                        </button>
                                    </div>
                                </form>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(EditActor);