import qs from 'qs';
import React, { Fragment } from 'react'
import { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { changeUserRole, getAllUsers } from '../../../actions/Users';

const ChangeRoles = () => {
    const dispatch = useDispatch();

    const USER = useSelector(state => state.user);
    const users = useSelector(state => state.users);

    if (USER.role !== "owner") {
        return < Redirect to="/admin" />
    }

    const handleChangeRoles = (event, userId) => {
        const user = {
            userId,
            role: event.target.value
        }

        dispatch(changeUserRole(user));
    }

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllUsers(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | تغییر نقش کاربران</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        ویرایش نقش کاربران
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {users.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>نام</th>
                                                    <th>نقش</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {users.map(user => user.role !== "owner"
                                                    ? (
                                                        <tr key={user._id}>
                                                            <td>
                                                                {user.fullname}
                                                                {user._id === USER._id
                                                                    ? (
                                                                        <span
                                                                            className='badge'
                                                                            style={{
                                                                                marginRight: "10px",
                                                                                backgroundColor: "#00bcd4"
                                                                            }}>خودم</span>
                                                                    )
                                                                    : null}
                                                            </td>
                                                            <td>
                                                                <form>
                                                                    <select
                                                                        name="role"
                                                                        id="roles"
                                                                        className='form-select'
                                                                        onChange={e => handleChangeRoles(e, user._id)}
                                                                        defaultValue={user.role}>
                                                                        <option value="user">کاربر</option>
                                                                        <option value="admin">مدیر</option>
                                                                        <option value="owner">رئیس</option>
                                                                    </select>
                                                                </form>
                                                            </td>
                                                        </tr>
                                                    )
                                                    : (null))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>کاربری جهت نمایش وجو ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default ChangeRoles;