import React from 'react'
import { Route, Switch } from 'react-router';
import { useSelector } from 'react-redux';
import AboutUs from '../components/AboutUs/AboutUs';
import Actors from '../components/Actors/Actors';
import SingleActor from '../components/Actors/SingleActor';
import Movies from '../components/Archive/Movies';
import ContactUs from '../components/ContactUs/ContactUs';
import Directors from '../components/Directors/Directors';
import SingleDirector from '../components/Directors/SingleDirector';
import HomePage from '../components/Home/HomePage';
import UserLayout from '../components/Layouts/UserLayout';
import Login from '../components/Login/Login';
import NotFound from '../components/NotFound/NotFound';
import Register from '../components/Register/Register';
import SingleMovie from '../components/SingleMovie/SingleMovie';
import UserProfile from '../components/UserProfile/UserProfile';
import Genres from '../components/Genres/Genres';
import Channels from '../components/Channels/Channels';
import NoPremission from '../components/NoPremission/NoPremission';
import Logout from '../components/Logout/Logout';
import AdminLayout from '../components/Layouts/AdminLayout';
import AdminPage from '../components/Admin/common/adminPage';
import AddMovie from '../components/Admin/Movies/AddMovie';
import DeleteMovie from '../components/Admin/Movies/DeleteMovie';
import DirectorsTable from '../components/Admin/Directors/DirectorsTable';
import AddDirector from '../components/Admin/Directors/AddDirector';
import EditDirector from '../components/Admin/Directors/EditDirector';
import DeleteDirector from '../components/Admin/Directors/DeleteDirector';
import MoviesTable from '../components/Admin/Movies/MoviesTable';
import EditMovie from '../components/Admin/Movies/EditMovie';
import ActorsTable from '../components/Admin/Actors/ActorsTable';
import AddActor from '../components/Admin/Actors/AddActor';
import EditActor from '../components/Admin/Actors/EditActor';
import DeleteActor from '../components/Admin/Actors/DeleteActor';
import AdminsTable from '../components/Admin/Users/AdminsTable';
import OwnersTable from '../components/Admin/Users/OwnersTable';
import ChangeRoles from '../components/Admin/Users/ChangeRoles';
import UsersTable from '../components/Admin/Users/UsersTable';
import EditUser from '../components/Admin/Users/EditUser';
import DeleteUser from '../components/Admin/Users/DeleteUser';
import DeleteComment from '../components/Admin/comments/DeleteComment';
import FilesTable from '../components/Admin/Files/FilesTable';
import EditFile from '../components/Admin/Files/EditFile';
import DeleteFile from '../components/Admin/Files/DeleteFile';
import AddFile from '../components/Admin/Files/AddFile';
import MessagesTable from '../components/Admin/Messages/MessagesTable';
import DeleteMessage from '../components/Admin/Messages/DeleteMessage';
import AnswerMessage from '../components/Admin/Messages/AnswerMessage';
import ForgotPassword from '../components/ForgotPassword/ForgotPassword';
import GenresTable from '../components/Admin/Genres/GenresTable';
import AddGenre from '../components/Admin/Genres/AddGenre';
import EditGenre from '../components/Admin/Genres/EditGenre';
import DeleteGenre from '../components/Admin/Genres/DeleteGenre';
import ChannelsTable from '../components/Admin/Channels/ChannelsTable';
import AddChannel from '../components/Admin/Channels/AddChannel';
import EditChannel from '../components/Admin/Channels/EditChannel';
import DeleteChannel from '../components/Admin/Channels/DeleteChannel';
import LimitsTable from '../components/Admin/AgeLimits/LimitsTable';
import AddLimit from '../components/Admin/AgeLimits/AddLimit';
import EditLimit from '../components/Admin/AgeLimits/EditLimit';
import DeleteLimit from '../components/Admin/AgeLimits/DeleteLimit';
import SeriesTable from '../components/Admin/Series/SeriesTable';
import AddSerial from '../components/Admin/Series/AddSerial';
import EditSerial from '../components/Admin/Series/EditSerial';
import DeleteSerial from '../components/Admin/Series/DeleteSerial';
import SingleSerial from '../components/SingleSerial/SingleSerial';
import EpisodesTable from '../components/Admin/Episodes/EpisodesTable';
import AddEpisode from '../components/Admin/Episodes/AddEpisode';
import EditEpisode from '../components/Admin/Episodes/EditEpisode';
import DeleteEpisode from '../components/Admin/Episodes/DeleteEpisode';
import Series from '../components/Archive/Series';
import DownloadPage from '../components/SingleSerial/Episodes/DownloadPage';
import ResetPassword from '../components/ForgotPassword/ResetPassword';

const MovieLand = () => {
    const user = useSelector(state => state.user);

    return (
        <Switch>
            <Route path={["/admin"]}>
                {user.role !== "user" && user.fullname
                    ? (
                        <AdminLayout>
                            <Switch>
                                <Route path="/admin" exact component={AdminPage} />

                                {/* MOVIES */}
                                <Route path="/admin/movies" exact component={MoviesTable} />
                                <Route path="/admin/movies/add-movie" component={AddMovie} />
                                <Route path="/admin/movies/edit-movie/:id" component={EditMovie} />
                                <Route path="/admin/movies/delete-movie/:id" component={DeleteMovie} />

                                {/* FILES */}
                                <Route path="/admin/files/:id" exact component={FilesTable} />
                                <Route path="/admin/files/add-file/:id" component={AddFile} />
                                <Route path="/admin/files/edit-file/:id" component={EditFile} />
                                <Route path="/admin/files/delete-file/:id" component={DeleteFile} />

                                {/* SERIES */}
                                <Route path="/admin/series" exact component={SeriesTable} />
                                <Route path="/admin/series/add-serial" component={AddSerial} />
                                <Route path="/admin/series/edit-serial/:id" component={EditSerial} />
                                <Route path="/admin/series/delete-serial/:id" component={DeleteSerial} />

                                {/* EPISODES */}
                                <Route path="/admin/series/episodes/:id" exact component={EpisodesTable} />
                                <Route path="/admin/series/episodes/add-episode/:id" component={AddEpisode} />
                                <Route path="/admin/series/episodes/edit-episode/:id" component={EditEpisode} />
                                <Route path="/admin/series/episodes/delete-episode/:id" component={DeleteEpisode} />

                                {/* COMMENTS */}
                                <Route path="/admin/comments/delete-comment/:id" component={DeleteComment} />

                                {/* DIRECTORS */}
                                <Route path="/admin/directors" exact component={DirectorsTable} />
                                <Route path="/admin/directors/add-director" component={AddDirector} />
                                <Route path="/admin/directors/edit-director/:id" component={EditDirector} />
                                <Route path="/admin/directors/delete-director/:id" component={DeleteDirector} />

                                {/* ACTORS */}
                                <Route path="/admin/actors" exact component={ActorsTable} />
                                <Route path="/admin/actors/add-actor" component={AddActor} />
                                <Route path="/admin/actors/edit-actor/:id" component={EditActor} />
                                <Route path="/admin/actors/delete-actor/:id" component={DeleteActor} />

                                {/* GENRES */}
                                <Route path="/admin/genres" exact component={GenresTable} />
                                <Route path="/admin/genres/add-genre" component={AddGenre} />
                                <Route path="/admin/genres/edit-genre/:id" component={EditGenre} />
                                <Route path="/admin/genres/delete-genre/:id" component={DeleteGenre} />

                                {/* CHANNELS */}
                                <Route path="/admin/channels" exact component={ChannelsTable} />
                                <Route path="/admin/channels/add-channel" component={AddChannel} />
                                <Route path="/admin/channels/edit-channel/:id" component={EditChannel} />
                                <Route path="/admin/channels/delete-channel/:id" component={DeleteChannel} />

                                {/* LIMITS */}
                                <Route path="/admin/age-limits" exact component={LimitsTable} />
                                <Route path="/admin/age-limits/add-limit" component={AddLimit} />
                                <Route path="/admin/age-limits/edit-limit/:id" component={EditLimit} />
                                <Route path="/admin/age-limits/delete-limit/:id" component={DeleteLimit} />

                                {/* MESSAGES */}
                                <Route path="/admin/messages" exact component={MessagesTable} />
                                <Route path="/admin/messages/answer-message/:id" component={AnswerMessage} />
                                <Route path="/admin/messages/delete-message/:id" component={DeleteMessage} />

                                {/* USERS */}
                                <Route path="/admin/admins" component={AdminsTable} />
                                <Route path="/admin/owners" component={OwnersTable} />
                                <Route path="/admin/users" exact component={UsersTable} />
                                <Route path="/admin/users/change-roles" component={ChangeRoles} />
                                <Route path="/admin/users/edit-user/:id" component={EditUser} />
                                <Route path="/admin/users/delete-user/:id" component={DeleteUser} />

                                {/* NOT FOUND */}
                                <Route path={"*"} component={NotFound} />
                            </Switch>
                        </AdminLayout>
                    )
                    : (<Route component={NoPremission} />)}
            </Route>
            <Route path={["/"]}>
                <UserLayout>
                    <Switch>
                        {/* MAIN */}
                        <Route path={"/"} exact component={HomePage} />
                        <Route path={"/about-us"} component={AboutUs} />
                        <Route path={"/contact-us"} component={ContactUs} />
                        <Route path={"/genres"} component={Genres} />
                        <Route path={"/channels"} component={Channels} />

                        {/* MOVIES */}
                        <Route path={"/movies"} exact component={Movies} />
                        <Route path={"/movies/:id"} component={SingleMovie} />
                        <Route path={"/movies/like-movie/:id"} component={SingleMovie} />
                        <Route path={"/movies/dislike-movie/:id"} component={SingleMovie} />

                        {/* SERIES */}
                        <Route path={"/series"} exact component={Series} />
                        <Route path={"/series/episode/:id"} component={DownloadPage} />
                        <Route path={"/series/:id"} component={SingleSerial} />

                        {/* USER */}
                        <Route path={"/user/login"} component={Login} />
                        <Route path={"/user/forgot-password"} component={ForgotPassword} />
                        <Route path={"/user/reset-password/:token"} component={ResetPassword} />
                        <Route path={"/user/logout"} component={Logout} />
                        <Route path={"/user/register"} component={Register} />
                        <Route path={"/user/profile"} component={UserProfile} />

                        {/* DIRECTORS */}
                        <Route path={"/directors"} exact component={Directors} />
                        <Route path={"/directors/:id"} component={SingleDirector} />

                        {/* ACTORS */}
                        <Route path={"/actors"} exact component={Actors} />
                        <Route path={"/actors/:id"} component={SingleActor} />

                        {/* NOT FOUND */}
                        <Route path={"/no-premission"} component={NoPremission} />
                        <Route path={"*"} component={NotFound} />
                    </Switch>
                </UserLayout>
            </Route>
        </Switch>
    );
}

export default MovieLand;