import React, { useEffect } from 'react';
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { convertToShamsi } from '../../../utils/shamsi';
import { getAllMessages } from '../../../actions/Messages';
import { truncate } from '../../../utils/truncate';

const MessagesTable = () => {
    const dispatch = useDispatch();
    const messages = useSelector(state => state.messages);
    console.log(messages)

    useEffect(() => {
        dispatch(getAllMessages());
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | پیام ها</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        پیام ها
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {messages.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>کاربر</th>
                                                    <th>تاریخ دریافت</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {messages.map(message => (
                                                    <tr key={message._id}>
                                                        <td>
                                                            <a>{truncate(message.message, 20)}</a>
                                                        </td>
                                                        <td>
                                                            <a>{message.user.fullname}</a>
                                                        </td>
                                                        <td>{convertToShamsi(message.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/messages/answer-message/${message._id}`}
                                                                className='btn btn-success'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                پاسخ دهی
                                                            </a>
                                                            <a
                                                                href={`/admin/messages/delete-message/${message._id}`}
                                                                className='btn btn-danger'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>پیامی جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default MessagesTable;