import { toast } from 'material-react-toastify';
import React, { Fragment, useRef, useState } from 'react'
import { Helmet } from 'react-helmet'
import { useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { sendMessage } from '../../Services/messageService';

const ContactUs = ({ history }) => {
    const user = useSelector(state => state.user);

    const [message,
        setMessage] = useState("");

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: " پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 5 کاراکتر باشد!",
            email: "ایمیل وارد شده معتبر نمی باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const handleContactUs = async event => {
        event.preventDefault();

        try {
            const { status } = await sendMessage({ message });

            if (status === 201) {
                toast.success("نظر شما با موفقيت ارسال شد");
                history.push("/");
            }
        } catch (err) {
            console.log(err);
            toast.error(JSON.parse(err.request.response).message);
        }
    }

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | تماس با ما
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/contact-us">ارتباط با ما</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <div className="col-md-12 col-sm-12 col-xs-12">
                            <section className="user-comments">
                                <header>
                                    <h3>
                                        تماس با ما
                                    </h3>
                                </header>
                                <div className="inner">
                                    {user.fullname
                                        ? (
                                            <form className='row g-3' onSubmit={e => handleContactUs(e)}>
                                                <div className="input-group">
                                                    <span class="input-group-text" id="basic-addon1">پيام شما</span>
                                                    <textarea
                                                        className="form-control"
                                                        placeholder="..."
                                                        rows={4}
                                                        name='message'
                                                        value={message}
                                                        onChange={e => {
                                                            setMessage(e.target.value);
                                                            validator
                                                                .current
                                                                .showMessageFor("message");
                                                        }}></textarea>
                                                </div>
                                                {validator
                                                    .current
                                                    .message("message", message, "required")}
                                                <br />

                                                <div className="input-group">
                                                    <div
                                                        className="g-recaptcha"
                                                        data-sitekey="6LdM7HghAAAAANM-6frxXMB7CW7xm1IAnU7Af9Z9"
                                                        data-theme="dark"></div>
                                                </div>

                                                <div className="input-group">
                                                    <div className="col-xs-12">
                                                        <button type="submit" className="btn btn-success">
                                                            ثبت دیدگاه
                                                        </button>
                                                    </div>
                                                </div>
                                            </form>
                                        )
                                        : (
                                            <div className='alert alert-info'>
                                                <p>جهت ارتباط با ما الزام است وارد سايت شويد</p>
                                            </div>
                                        )}
                                </div>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(ContactUs);