import React, { useEffect } from 'react';
import { Fragment } from 'react';
import Helmet from 'react-helmet';
import { useSelector, useDispatch } from 'react-redux';
import { convertToShamsi } from '../../../utils/shamsi';
import { getAllGenres } from '../../../actions/Genres';
import qs from 'qs';

const GenresTable = () => {
    const dispatch = useDispatch();

    const genres = useSelector(state => state.genres);

    const searchText = qs
        .parse(location.search, { ignoreQueryPrefix: true })
        .search;

    useEffect(() => {
        dispatch(getAllGenres(searchText));
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | ژانر ها</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        className='table-header'
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        ژانر ها

                        <a href={`/admin/genres/add-genre`} className='btn btn-success'>
                            ژانر جدید
                        </a>
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                {genres.length > 0
                                    ? (
                                        <table className='table table-dark table-hover'>
                                            <thead>
                                                <tr>
                                                    <th>عنوان</th>
                                                    <th>تاریخ ساخت</th>
                                                    <th>مدیریت</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {genres.map(genre => (
                                                    <tr key={genre._id}>
                                                        <td>
                                                            <a target={'_blank'} href={`/movies/genres/${genre._id}`}>{genre.title}</a>
                                                        </td>
                                                        <td>{convertToShamsi(genre.createdAt)}</td>
                                                        <td>
                                                            <a
                                                                href={`/admin/genres/edit-genre/${genre._id}`}
                                                                className='btn btn-warning'
                                                                style={{
                                                                    marginLeft: "10px"
                                                                }}>
                                                                ویرایش
                                                            </a>
                                                            <a href={`/admin/genres/delete-genre/${genre._id}`} className='btn btn-danger'>
                                                                حذف
                                                            </a>
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    )
                                    : (
                                        <div className="alert alert-info">
                                            <p>ژانری جهت نمایش وجود ندارد !</p>
                                        </div>
                                    )}
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default GenresTable;