import React, { Fragment, useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet'
import { useDispatch } from 'react-redux';
import SimpleReactValidator from 'simple-react-validator';
import { updateUser } from '../../actions/User';

const EditProfile = ({ user }) => {
    const dispatch = useDispatch();

    const [fullname, setFullname] = useState(user.fullname);
    const [email, setEmail] = useState(user.email);
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 6 کاراکتر باشد!",
            email: "ایمیل معتبر نمی باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const handleEditUser = event => {
        event.preventDefault();

        const USER = new FormData();
        USER.append("fullname", fullname);
        USER.append("email", email);
        USER.append("password", password);
        USER.append("confirmPassword", confirmPassword);
        USER.append("avatar", document.getElementById("avatar-upload").files[0]);

        dispatch(updateUser(USER, user._id));
    }

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | ویرایش پروفایل
                </title>
            </Helmet>
            <section className="user-account-content">
                <header>
                    <h1>
                        ویرایش حساب کاربری
                    </h1>
                </header>
                <div className="inner form-layer">
                    <form onSubmit={e => handleEditUser(e)}>

                        <div className="row">
                            <div className="col-md-7 col-sm-8 col-xs-12">
                                <div className="input-group">
                                    <span className="input-group-text" id="username">
                                        نام كامل
                                    </span>
                                    <input
                                        type="text"
                                        name='fullname'
                                        value={fullname}
                                        onChange={e => {
                                            setFullname(e.target.value);
                                            validator.current.showMessageFor("fullname");
                                        }}
                                        className="form-control"
                                        placeholder="نام و نام خانوادگی"
                                        aria-describedby="username" />
                                </div>
                                {validator.current.message("fullname", fullname, "required|min:4")}
                            </div>

                            <div className="col-md-7 col-sm-8 col-xs-12">
                                <div className="input-group">
                                    <span className="input-group-text" id="email-address">
                                        ايميل
                                    </span>
                                    <input
                                        type="text"
                                        name='email'
                                        value={email}
                                        onChange={e => {
                                            setEmail(e.target.value);
                                            validator.current.showMessageFor("email");
                                        }}
                                        className="form-control"
                                        placeholder="ایمیل"
                                        aria-describedby="email-address" />
                                </div>
                                {validator.current.message("email", email, "required|email")}
                            </div>

                            <div className="col-md-7 col-sm-8 col-xs-12">
                                <div className="input-group">
                                    <span className="input-group-text" id="password">
                                        رمز
                                    </span>
                                    <input
                                        type="password"
                                        name='password'
                                        value={password}
                                        onChange={e => {
                                            setPassword(e.target.value);
                                            validator.current.showMessageFor("password");
                                        }}
                                        className="form-control"
                                        placeholder="رمز عبور "
                                        aria-describedby="password" />
                                </div>
                                {validator.current.message("password", password, "min:6")}
                            </div>

                            <div className="col-md-7 col-sm-8 col-xs-12">
                                <div className="input-group">
                                    <span className="input-group-text" id="username">
                                        تاييد رمز
                                    </span>
                                    <input
                                        type="password"
                                        name='confirmPassword'
                                        value={confirmPassword}
                                        onChange={e => {
                                            setConfirmPassword(e.target.value);
                                            validator.current.showMessageFor("confirmPassword");
                                        }}
                                        className="form-control"
                                        placeholder="تایید رمز عبور"
                                        aria-describedby="confirmPassword" />
                                </div>
                                {validator.current.message("confirmPassword", confirmPassword, "min:6")}
                            </div>

                            <div className="col-md-7 col-sm-8 col-xs-12">
                                <div className="input-group">
                                    <span className="input-group-text" id="password">
                                        آواتار
                                    </span>
                                    <input
                                        type="file"
                                        name='avatar'
                                        id='avatar-upload'
                                        className="form-control"
                                        aria-describedby="avatar" />
                                </div>
                            </div>

                            <div className="col-md-7 col-sm-8 col-xs-12">
                                <button className="btn btn-success">
                                    ویرایش اطلاعات
                                </button>
                                <a href='/profile' style={{ marginRight: "10px", border: "none" }} className="btn btn-warning">
                                    انصراف
                                </a>
                            </div>
                        </div>

                    </form>
                </div>
            </section>
        </Fragment>
    );
}

export default EditProfile;