import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Age Limits
export const getLimits = searchText => {
    return http.get(`${local}/age-limits?search=${searchText}`)
}

// Gets Single Age Limit
export const getLimit = limitId => {
    return http.get(`${local}/admin/age-limits/${limitId}`);
}

// Add Age Limit
export const newLimit = limit => {
    return http.post(`${local}/admin/age-limits/add-limit`, JSON.stringify(limit));
}

// Edit Age Limit
export const editLimit = (limitId, limit) => {
    return http.put(`${local}/admin/age-limits/edit-limit/${limitId}`, JSON.stringify(limit), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}

// Remove Age Limit
export const removeLimit = limitId => {
    return http.delete(`${local}/admin/age-limits/remove-limit/${limitId}`);
}