import { random } from 'lodash';
import { toast } from 'material-react-toastify';
import React, { Fragment, useEffect, useRef, useState } from 'react'
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { getSingleFile } from '../../../actions/File';
import { updateFile } from '../../../actions/Files';
import { idValidator } from '../../../utils/idValidator';

const EditFile = ({ match, history }) => {
    const dispatch = useDispatch();
    const fileId = match.params.id;

    const file = useSelector(state => state.file);

    const [showName,
        setShowName] = useState("");
    const [quality,
        setQuality] = useState("");
    const [features,
        setFeatures] = useState("");
    const [,
        forceUpdate] = useState(0);

    const qualities = ["4k", "1080p", "720p", "480p"];

    const FEATS = ["original", "sensored", "dubbed", "hardsubed", "softsubed"];

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد",
            min: "کمتر از 5 کاراکتر نباید باشد"
        },
        element: message => <div
            className='alert alert-danger'
            style={{
                marginTop: "5px"
            }}>
            {message}
        </div>
    }));

    const handleEditFile = event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                const FILE = new FormData();
                FILE.append("showName", showName);
                FILE.append("quality", quality);
                FILE.append("features", features);
                FILE.append("file", document.getElementById("movie-file").files[0]);

                dispatch(updateFile(fileId, FILE));
                history.replace(`/admin/files/${fileId}`);
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error(JSON.parse(err.request.response).message);
        }
    }

    useEffect(() => {
        if (idValidator(fileId)) {
            dispatch(getSingleFile(fileId));
        } else {
            return history.goBack();
        }
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | ويرايش فایل</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        ويرايش فایل
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <form onSubmit={e => handleEditFile(e)}>
                                    <div class="input-group">
                                        <span class="input-group-text" id="basic-addon1">نام نمایشی</span>
                                        <input
                                            type="text"
                                            name='showName'
                                            value={showName}
                                            placeholder={file.showName}
                                            class="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setShowName(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("showName");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("showName", showName, "required|min:4")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">کیفیت</span>
                                        <select
                                            name="quality"
                                            id="qualities"
                                            className='form-select'
                                            onChange={e => {
                                                setQuality(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("quality");
                                            }}>
                                            {qualities.map(quality => quality === file.quality
                                                ? (
                                                    <option key={quality} value={quality} selected>{quality}</option>
                                                )
                                                : (
                                                    <option key={quality} value={quality}>{quality}</option>
                                                ))}
                                        </select>
                                    </div>
                                    {validator
                                        .current
                                        .message("quality", quality, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">ویژگی ها</span>
                                        <select
                                            name="features"
                                            id="features"
                                            className='form-select'
                                            onChange={e => {
                                                setFeatures(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("features");
                                            }}>
                                            {FEATS.map(feature => feature === file.feature
                                                ? (
                                                    <option key={feature} value={feature} selected>{feature}</option>
                                                )
                                                : (
                                                    <option key={feature} value={feature}>{feature}</option>
                                                ))}
                                        </select>
                                    </div>
                                    {validator
                                        .current
                                        .message("features", features, "required")}
                                    <br />

                                    <div class="input-group">
                                        <span class="input-group-text" id="basic-addon1">فایل</span>
                                        <input
                                            type="file"
                                            name='file'
                                            class="form-control"
                                            aria-describedby="basic-addon1"
                                            id='movie-file' />
                                    </div>
                                    <br />

                                    <button type='submit' className='btn btn-success'>
                                        تایید فایل
                                    </button>

                                    <button
                                        className='btn btn-warning'
                                        style={{
                                            marginRight: "10px"
                                        }}
                                        onClick={() => history.push(`/admin/files/${file.movieId}`)}>
                                        انصراف
                                    </button>
                                </form>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(EditFile);