import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Genres
export const getGenres = searchText => {
    return http.get(`${local}/genres?search=${searchText}`)
}

// Gets Single Genre
export const getGenre = genreId => {
    return http.get(`${local}/admin/genres/${genreId}`);
}

// Add Genre
export const newGenre = genre => {
    return http.post(`${local}/admin/genres/add-genre`, JSON.stringify(genre));
}

// Edit Genre
export const editGenre = (genreId, genre) => {
    return http.put(`${local}/admin/genres/edit-genre/${genreId}`, JSON.stringify(genre), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}

// Remove Genre
export const removeGenre = genreId => {
    return http.delete(`${local}/admin/genres/remove-genre/${genreId}`);
}