import { random } from 'lodash';
import { toast } from 'material-react-toastify';
import React, { Fragment, useEffect, useState } from 'react'
import { useRef } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import SimpleReactValidator from 'simple-react-validator';
import { getSingleUser } from '../../../actions/AdminUser';
import { updateUSER } from '../../../actions/Users';
import config from '../../../Services/config.json';
import { idValidator } from '../../../utils/idValidator';

const EditUser = ({ match, history }) => {
    const dispatch = useDispatch();
    const userId = match.params.id;

    const user = useSelector(state => state.user);
    const USER = useSelector(state => state.adminUser);

    if (!USER) {
        history.goBack();
    }

    if (user.role === "admin" && USER.role !== "user") {
        history.goBack();
    }

    const [fullname,
        setFullname] = useState("");
    const [role,
        setRole] = useState("");
    const [,
        forceUpdate] = useState(0);

    const roles = ["user", "admin", "owner"]

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد",
            min: "کمتر از 5 کاراکتر نباید باشد"
        },
        element: message => <div
            className='alert alert-danger'
            style={{
                marginTop: "5px"
            }}>
            {message}
        </div>
    }));

    const handleEditUser = e => {
        e.preventDefault();

        try {
            if (validator.current.allValid()) {
                const updatedUser = new FormData();
                updatedUser.append("fullname", fullname);
                updatedUser.append("role", role);
                updatedUser.append("avatar", document.getElementById("avatar-upload").files[0]);

                dispatch(updateUSER(updatedUser, USER._id));
                history.push("/admin/users");
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(0, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error("مشکلی هنگام ویرایش کردن کاربر رخ داد!");
        }
    }

    useEffect(() => {
        if (idValidator(userId)) {
            dispatch(getSingleUser(userId));
        } else {
            return history.goBack();
        }
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>مووی لند | ویرایش کاربر</title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        ویرایش کاربر
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <div className="inner">
                                    <form className='row g-3' onSubmit={e => handleEditUser(e)}>
                                        <div className="input-group">
                                            <span className="input-group-text" id="username">
                                                <i className="zmdi zmdi-account"></i>
                                            </span>
                                            <input
                                                type="text"
                                                name="fullname"
                                                placeholder={USER.fullname}
                                                className="form-control"
                                                aria-describedby="username"
                                                value={fullname}
                                                onChange={e => {
                                                    setFullname(e.target.value);
                                                    validator
                                                        .current
                                                        .showMessageFor("fullname");
                                                }} />
                                        </div>
                                        {validator
                                            .current
                                            .message("fullname", fullname, "required|min:5")}
                                        <br /> {user.role === "owner"
                                            ? (
                                                <Fragment>
                                                    <div className="input-group">
                                                        <span className="input-group-text" id="password">نقش کاربر</span>
                                                        <select
                                                            name="role"
                                                            id="roles"
                                                            className="form-select"
                                                            onChange={e => {
                                                                setRole(e.target.value);
                                                                validator
                                                                    .current
                                                                    .showMessageFor("role");
                                                            }}>
                                                            <option value={"placeholder"}>انتخاب کنید</option>
                                                            {roles.map(role => USER.role === role
                                                                ? (
                                                                    <option key={role} value={role} selected>{role}</option>
                                                                )
                                                                : (
                                                                    <option key={role} value={role}>{role}</option>
                                                                ))}
                                                        </select>
                                                    </div>
                                                    {validator
                                                        .current
                                                        .message("role", role, "required")}
                                                    < br />
                                                </Fragment>
                                            )
                                            : (null)
                                        }

                                        {user.avatar
                                            ? (
                                                <div className="input-group">
                                                    <div className='alert alert-info'>
                                                        <a target={'_blank'} href={`${config.localapi}/avatars/${user.avatar}`}>مشاهده ي آواتار كاربر</a>
                                                    </div>
                                                </div>
                                            )
                                            : (null)}

                                        <div className="input-group">
                                            <span class="input-group-text" id="basic-addon1">آواتار</span>
                                            <input
                                                type="file"
                                                name="avatar"
                                                className="form-control"
                                                aria-describedby="avatar"
                                                id="avatar-upload" />
                                        </div>
                                        <br />

                                        <div className="input-group">
                                            <button type='submit' className="btn btn-success col-md-3">
                                                تایید
                                            </button>

                                            <Link
                                                to="/Admin/Users"
                                                style={{
                                                    marginRight: "10px"
                                                }}
                                                className='btn btn-warning col-md-3'>
                                                انصراف
                                            </Link>
                                        </div>

                                    </form>
                                </div>
                            </section>
                        </div>
                    </div>
                </section>
            </div>

        </Fragment>
    );
}

export default withRouter(EditUser);