import React, { Fragment, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getRelatedFiles } from '../../actions/Files';
import config from '../../Services/config.json';
import { addMovieDownload } from '../../actions/Movie';

const DownloadBox = ({ movieId }) => {
    const dispatch = useDispatch();
    const files = useSelector(state => state.files);

    useEffect(() => {
        dispatch(getRelatedFiles(movieId));
    }, []);

    return (

        <Fragment>
            <div className="statistics">
                <h2 className='dlbox-header'>
                    دانلود باکس
                </h2>

                {files.length > 0
                    ? files.map(file => (
                        <a
                            key={file._id}
                            href={`${config.localapi}/files/${file.fileName}`}
                            onClick={() => dispatch(addMovieDownload(movieId))}>
                            {file.showName}
                        </a>
                    ))
                    : (
                        <div className="alert alert-info">
                            <p>فایلی جهت دانلود وجود ندارد !</p>
                        </div>
                    )}
            </div>
        </Fragment>
    );
}

export default DownloadBox;