import { getDirector } from "../Services/directorService"
import { getMovieDirector } from "../Services/movieService";
import { getSerialDirector } from "../Services/serialService";

export const getSingleDirector = directorId => {
    return async dispatch => {
        const { data } = await getDirector(directorId);
        await dispatch({ type: "GET_DIRECTOR", payload: data.director });
    }
}

export const getSingleMovieDirector = movieId => {
    return async dispatch => {
        const { data } = await getMovieDirector(movieId);
        await dispatch({ type: "GET_DIRECTOR", payload: data.director });
    }
}

export const getSingleSerialDirector = serialId => {
    return async dispatch => {
        const { data } = await getSerialDirector(serialId);
        await dispatch({ type: "GET_DIRECTOR", payload: data.director });
    }
}