import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Series
export const getSeries = (searchText, genre, sort, director, actor, channel) => {
    return http.get(`${local}/series?search=${searchText}&genre=${genre}&sort=${sort}&director=${director}&actor=${actor}&channel=${channel}`);
}

// Gets Single Serial
export const getSerial = serialId => {
    return http.get(`${local}/series/${serialId}`);
}

// Gets Serial Director
export const getSerialDirector = serialId => {
    return http.get(`${local}/series/serial-director/${serialId}`);
}

// Gets Serial Actors
export const getSerialActors = serialId => {
    return http.get(`${local}/series/serial-actors/${serialId}`);
}

// Gets Serial Genre
export const getSerialGenre = serialId => {
    return http.get(`${local}/series/serial-genre/${serialId}`);
}

// Gets Serial Channel
export const getSerialChannel = serialId => {
    return http.get(`${local}/series/serial-channel/${serialId}`);
}

// Gets Serial Limit
export const getSerialLimit = serialId => {
    return http.get(`${local}/series/serial-limit/${serialId}`);
}

// Add Serial
export const newSerial = serial => {
    return http.post(`${local}/admin/series/add-serial`, serial, {
        headers: {
            "Content-Type": "multipart/form-data"
        }
    });
}

// Edit Serial
export const editSerial = (serialId, serial) => {
    return http.put(`${local}/admin/series/edit-serial/${serialId}`, serial);
}

// React To Serial
export const reactToSerial = (serialId, method) => {
    return http.put(`${local}/series/react-to-serial/${serialId}`, JSON.stringify(method), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}

// Remove Movies
export const addSerialDownloads = serialId => {
    return http.put(`${local}/series/add-download/${serialId}`);
}

// Remove Movies
export const removeSerial = serialId => {
    return http.delete(`${local}/admin/series/remove-serial/${serialId}`);
}