import { toast } from "material-react-toastify";
import { getEpisode } from "../Services/episodeService";

export const getSingleEpisode = episodeId => {
    return async dispatch => {
        try {
            const { data } = await getEpisode(episodeId);
            await dispatch({ type: "GET_EPISODE", payload: data.episode });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری قسمت مورد نظر با مشکل مواجه شد!");
        }
    }
}