export const limitsReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_LIMITS":
            return [...action.payload];
        case "ADD_LIMIT":
            return [...action.payload];
        case "EDIT_LIMIT":
            return [...action.payload];
        case "DELETE_LIMIT":
            return [...action.payload];
        default:
            return state;
    }
}