import React from 'react'
import { withRouter } from 'react-router-dom';
import { useSelector } from 'react-redux';

const AdminNavbar = ({ location }) => {
    const user = useSelector(state => state.user);

    return (
        <nav className="navbar navbar-expand-lg bg-dark">
            <div className="container-fluid">
                <a className="navbar-brand" href="/admin">پنل مدیریت</a>
                <button
                    className="navbar-toggler"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li
                            className={location.pathname === "/admin/movies"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/movies">
                                فیلم ها
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/series"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/series">
                                سریال ها
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/directors"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/directors">
                                کارگردانان
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/actors"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/actors">
                                بازیگران
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/genres"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/genres">
                                ژانر ها
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/age-limits"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/age-limits">
                                محدودیت سنی
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/channels"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/channels">
                                شبکه ها
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/messages"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/messages">
                                پیام ها
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/owners"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/owners">
                                رئیسان
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/admins"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/admins">
                                مدیران
                            </a>
                        </li>
                        <li
                            className={location.pathname === "/admin/users"
                                ? "nav-item active"
                                : "nav-item"}>
                            <a className='nav-link' href="/admin/users">
                                کاربران
                            </a>
                        </li>
                        <li className='nav-item'>
                            <a href="/" className='btn-back-home nav-link'>
                                بازگشت به سایت
                            </a>
                        </li>
                    </ul>

                    {location.pathname === "/admin/movies" || location.pathname === "/admin/series" || location
                        .pathname
                        .includes("episodes") || location.pathname === "/admin/directors" || location.pathname === "/admin/actors" || location.pathname === "/admin/genres" || location.pathname === "/admin/channels" || location.pathname === "/admin/age-limits" || location.pathname === "/admin/users" || location.pathname === "/admin/admins" || location.pathname === "/admin/owners" || location.pathname === "/admin/users/change-roles" || location
                            .pathname
                            .includes("/admin/files")
                        ? (
                            <form action={location.pathname} className="d-flex admin-search" role="search">
                                <input
                                    className="form-control me-2"
                                    name='search'
                                    type="search"
                                    placeholder="جستجو"
                                    aria-label="Search" />
                                <button className="btn btn-outline-success" type="submit">برو</button>
                            </form>
                        )
                        : (
                            <div className="clientarea-admin">
                                <div className="loggein">
                                    <i className="zmdi zmdi-account client-icon"></i>{" "}
                                    <a href="/user/profile" className='client-link'>
                                        {user.fullname}({user.role})
                                    </a>
                                    {" "}
                                    <span className='clientarea-spliter'>/</span>
                                    {" "}
                                    <a href="/user/logout" className='client-link'>خروج</a>
                                </div>
                            </div>
                        )}
                </div>
            </div>
        </nav>
    );
}

export default withRouter(AdminNavbar);