import React from 'react'
import MovieCard from '../Card/MovieCard';

const ActorMovies = ({ actorMovies, actorName }) => {
    return (
        <section className="user-comments">
            <header>
                <h3>
                    فیلم های این بازیگر
                </h3>
            </header>
            <div className="inner">
                {actorMovies.length > 0
                    ? (
                        <div className="row">
                            {actorMovies.map(movie => <MovieCard movie={movie} key={movie._id} />)}
                            <div className="col-xs-12">
                                <a href={`/movies?actor=${actorName}`} className="btn btn-success">مشاهده همه</a>
                            </div>
                        </div>
                    )
                    : (
                        <div className='alert alert-info'>
                            <p>فيلمي براي نمايش وجود ندارد !</p>
                        </div>
                    )}
            </div>
        </section>
    );
}

export default ActorMovies;