import React from 'react'
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { deleteDirector } from '../../../actions/Directors';
import { idValidator } from '../../../utils/idValidator';

const DeleteDirector = ({ match, history }) => {
    const dispatch = useDispatch();
    const directorId = match.params.id;

    useEffect(() => {
        if (idValidator(directorId)) {
            if (confirm("آیا از حذف کردن این کارگردان مطئنید ؟")) {
                dispatch(deleteDirector(match.params.id));
                history.replace("/admin/directors");
            }
            else {
                history.replace("/admin/directors");
            }
        } else {
            return location.replace("/admin/directors");
        }
    }, []);

    return null;
}

export default withRouter(DeleteDirector);