import React from 'react'
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { deleteLimit } from '../../../actions/AgeLimits';
import { idValidator } from '../../../utils/idValidator';

const DeleteLimit = ({ match, history }) => {
    const dispatch = useDispatch();
    const limitId = match.params.id;

    useEffect(() => {
        if (idValidator(limitId)) {
            if (confirm("آیا از حذف کردن این محدودیت مطئنید ؟")) {
                dispatch(deleteLimit(limitId));
                history.replace("/admin/age-limits");
            }
            else {
                history.replace("/admin/age-limits");
            }
        } else {
            return location.replace("/admin/age-limits");
        }
    }, []);

    return null;
}

export default withRouter(DeleteLimit);