import { toast } from "material-react-toastify";
import { addDownload, getMovie, reactToMovie } from "../Services/movieService";

export const getSingleMovie = movieId => {
    return async dispatch => {
        try {
            const { data } = await getMovie(movieId);
            await dispatch({ type: "GET_MOVIE", payload: data.movie });
        } catch (err) {
            console.log(err);
            toast.error("فیلم مورد نظر پیدا نشد !");
        }
    }
}

export const reactionToMovie = (movieId, method) => {
    return async dispatch => {
        try {
            const { data, status } = await reactToMovie(movieId, method);

            if (status === 200) {
                await dispatch({ type: "EDIT_MOVIE", payload: data.movie });
                toast.success("نظر كاربر با موفقيت ثبت شد");
            }
        } catch (err) {
            console.log(err);
            toast.error("نظرسنجي كارب با مشكل مواجه شد!");
        }
    }
}

export const addMovieDownload = movieId => {
    return async dispatch => {
        try {
            const { data, status } = await addDownload(movieId);

            if (status === 200) {
                await dispatch({ type: "EDIT_MOVIE", payload: data.movie });
                toast.success("فیلم مورد نظر با موفقیت دانلود شد !");
            }
        } catch (err) {
            console.log(err);
            toast.error("دانلود فیلم مورد نظر با مشکل مواجه شد !");
        }
    }
}