import React, { Fragment, useEffect } from 'react'
import Comments from '../Comments/Comments';
import DirectorInfo from '../../components/common/DirectorInfo';
import DownloadBox from '../../components/common/DownloadBox';
import MovieInfo from '../../components/common/MovieInfo';
import Rating from '../../components/common/Rating';
import Share from '../../components/common/Share';
import Tags from '../../components/common/Tags';
import config from '../../Services/config.json';
import { Helmet } from 'react-helmet';
import { getSingleMovie } from '../../actions/Movie';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, withRouter } from 'react-router';
import { getRelatedComments } from '../../actions/Comments';
import MovieActors from './MovieActors';
import { idValidator } from '../../utils/idValidator';
import { isEmpty } from 'lodash';
import { getSingleMovieGenre } from '../../actions/Genre';


const SingleMovie = ({ match }) => {
    const movieId = match.params.id;
    const dispatch = useDispatch();

    const user = useSelector(state => state.user);
    const movie = useSelector(state => state.movie);
    const comments = useSelector(state => state.comments);
    const genre = useSelector(state => state.genre);

    useEffect(() => {
        if (idValidator(movieId)) {
            dispatch(getSingleMovie(movieId));
            dispatch(getRelatedComments(movieId));
            dispatch(getSingleMovieGenre(movieId));
        } else {
            return location.replace("/");
        }

    }, []);

    // if (isEmpty(movie)) {
    //     return <Redirect to={"/"} />;
    // }

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | فیلم
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/movies">{genre.title}</a>
                            </li>
                            <li className="breadcrumb-item active" aria-current="page">
                                <a href={`/movies/${movie._id}`}>{movie.title}</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <div className="col-md-8 col-sm-12 col-xs-12 pull-left">
                            <section className="term-description">
                                <img src={`${config.localapi}/thumbnails/${movie.thumbnail}`} />
                                <h2>
                                    {movie.title}
                                </h2>
                                <p>
                                    {movie.shortInfo}
                                </p>

                                <MovieActors movieId={movieId} />
                            </section>

                            <Comments comments={comments} movieId={movieId} />
                        </div>
                        <aside className="col-md-4 col-sm-12 col-xs-12 pull-right">
                            <DirectorInfo movieId={movieId} />
                            <MovieInfo movie={movie} movieId={movieId} />
                            <DownloadBox movieId={movieId} />

                            {user.fullname
                                ? (<Rating movieId={movieId} />)
                                : (<div className="statistics">
                                    <h2>
                                        نظر سنجی
                                    </h2>
                                    <hr />

                                    <div className='alert alert-info'>
                                        <p>جهت نظردهي بايد وارد سايت شويد</p>
                                    </div>
                                </div>)
                            }
                        </aside>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(SingleMovie);