import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// All Related Episodes
export const getEpisodes = (serialId, searchText, season) => {
    return http.get(`${local}/series/episodes/${serialId}?season=${season}&search=${searchText}`);
}

// Single Episode
export const getEpisode = episodeId => {
    return http.get(`${local}/series/episodes/single-episode/${episodeId}`);
}

// Add Episode
export const newEpisode = (serialId, episode) => {
    return http.post(`${local}/admin/series/episodes/add-episode/${serialId}`, JSON.stringify(episode));
}

//Edit Episode
export const editEpisode = (episodeId, episode) => {
    return http.put(`${local}/admin/series/episodes/edit-episode/${episodeId}`, JSON.stringify(episode), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}

// Remove Episode
export const removeEpisode = episodeId => {
    return http.delete(`${local}/admin/series/episodes/remove-episode/${episodeId}`);
}