import React, { Fragment, useEffect } from 'react'
import Comments from '../Comments/Comments';
import DirectorInfo from '../common/DirectorInfo';
import Share from '../common/Share';
import Tags from '../common/Tags';
import config from '../../Services/config.json';
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, withRouter } from 'react-router';
import { getRelatedComments } from '../../actions/Comments';
import { idValidator } from '../../utils/idValidator';
import { isEmpty } from 'lodash';
import { getSingleSerialGenre } from '../../actions/Genre';
import { getSingleSerial } from '../../actions/Serial';
import SerialActors from './SerialActors';
import SerialInfo from '../common/SerialInfo';
import SerialRating from './SerialRating';
import Episodes from './Episodes/Episodes';

const SingleSeries = ({ match }) => {
    const serialId = match.params.id;
    const dispatch = useDispatch();

    const serial = useSelector(state => state.serial);
    const user = useSelector(state => state.user);
    const comments = useSelector(state => state.comments);
    const genre = useSelector(state => state.genre);

    useEffect(() => {
        if (idValidator(serialId)) {
            dispatch(getSingleSerial(serialId));
            dispatch(getRelatedComments(serialId));
            dispatch(getSingleSerialGenre(serialId));
        } else {
            return location.replace("/");
        }

    }, []);

    // if (isEmpty(serial)) {     return <Redirect to={"/"} />; }

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | سریال
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/series">{genre.title}</a>
                            </li>
                            <li className="breadcrumb-item active" aria-current="page">
                                <a href={`/series/${serial._id}`}>{serial.title}</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <div className="col-md-8 col-sm-12 col-xs-12 pull-left">
                            <section className="term-description">
                                <img src={`${config.localapi}/thumbnails/${serial.thumbnail}`} />
                                <h2>
                                    {serial.title}
                                </h2>
                                <p>
                                    {serial.shortInfo}
                                </p>

                                <Episodes seasons={serial.seasons} serialId={serialId} />

                                <SerialActors serialId={serialId} />
                            </section>

                            <Comments comments={comments} movieId={serialId} />
                        </div>
                        <aside className="col-md-4 col-sm-12 col-xs-12 pull-right">
                            <DirectorInfo movieId={serialId} />
                            <SerialInfo serial={serial} serialId={serialId} />
                            {user.fullname
                                ? (<SerialRating serialId={serialId} />)
                                : (
                                    <div className="statistics">
                                        <h2>
                                            نظر سنجی
                                        </h2>
                                        <hr />

                                        <div className='alert alert-info'>
                                            <p>جهت نظردهي بايد وارد سايت شويد</p>
                                        </div>
                                    </div>
                                )
                            }
                        </aside>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(SingleSeries);