import React, { Fragment, useEffect } from 'react'
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, withRouter } from 'react-router';
import Rating from '../../components/common/Rating';
import Share from '../../components/common/Share';
import Tags from '../../components/common/Tags';
import DirectorMovies from './DirectorMovies';
import { getSingleDirector } from '../../actions/Director';
import config from '../../Services/config.json';
import { getAllMovies } from '../../actions/Movies';
import { idValidator } from '../../utils/idValidator';
import { isEmpty } from 'lodash';

const SingleDirector = ({ match }) => {
    const directorId = match.params.id;
    const dispatch = useDispatch();

    const director = useSelector(state => state.director);
    const movies = useSelector(state => state.movies);
    const directorMovies = movies.filter(movie => movie.director._id === director._id);

    useEffect(() => {
        if (idValidator(directorId)) {
            dispatch(getSingleDirector(directorId));
            dispatch(getAllMovies());
        } else {
            return location.replace("/directors");
        }
    }, []);

    // if (isEmpty(director)) {     return <Redirect to={"/"} />; }

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | کارگردان
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/directors">كارگردانان</a>
                            </li>
                            <li className="breadcrumb-item active" aria-current="page">
                                <a href={`/directors/${director._id}`}>{director.fullname}</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-content">
                    <div className="row">
                        <div className="col-md-4 col-sm-12 col-xs-12 pull-left">
                            <section className="term-description">
                                <div>
                                    <img src={`${config.localapi}/portraits/${director.portrait}`} />

                                    <h2>
                                        {director.fullname}
                                    </h2>
                                    <p>
                                        {director.shortInfo}
                                    </p>
                                </div>
                            </section>

                        </div>

                        <aside className="col-md-8 col-sm-12 col-xs-12 pull-right">
                            <DirectorMovies
                                directorMovies={directorMovies}
                                directorName={director.fullname} />
                            {/* <Rating /> */}
                        </aside>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(SingleDirector);