import { toast } from "material-react-toastify";
import { editSerial, getSeries, newSerial, removeSerial } from "../Services/serialService";

export const getAllSeries = (searchText, genre, sort, director, actor, channel) => {
    return async dispatch => {
        const { data } = await getSeries(searchText, genre, sort, director, actor, channel);
        await dispatch({ type: "GET_SERIES", payload: data.series });
    }
}

export const addSerial = serial => {
    return async(dispatch, getState) => {
        const series = [...getState().series];

        try {
            const { data, status } = await newSerial(serial);

            if (status === 201) {
                await dispatch({ type: "ADD_SERIAL", payload: data.series });
                toast.success("سریال با موفقیت اضافه شد");
            }
        } catch (err) {
            await dispatch({ type: "ADD_SERIAL", payload: series });
            toast.error("عملیات اضافه کردن سریال با مشکل مواجه شد");
            console.log(err);
        }
    }
}

export const updateSerial = (serial, serialId) => {
    return async(dispatch, getState) => {
        const series = [...getState().series];

        try {
            const { data, status } = await editSerial(serialId, serial);

            if (status === 200) {
                await dispatch({ type: "UPDATE_SERIAL", payload: data.series });
                toast.success("سریال مورد نظر با موفقیت ویرایش شد");
            }
        } catch (err) {
            await dispatch({ type: "UPDATE_SERIAL", payload: series });
            toast.error("عملیات ویرایش کتاب با مشکل مواجه شد !");
            console.log(err);
        }
    }
}

export const deleteSerial = serialId => {
    return async(dispatch, getState) => {
        const series = [...getState().series];

        try {
            const { data, status } = await removeSerial(serialId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_SERIAL", payload: data.series });
                toast.success("سریال مورد نظر با موفقیت پاک شد.");
            }
        } catch (err) {
            await dispatch({ type: "REMOVE_SERIAL", payload: series });
            console.log(err);
            toast.error("عملیات پاکسازی سریال با مشکل مواجه شد!");
        }
    }
}