import React from 'react'
import config from '../../Services/config.json';
import { formatDate } from '../../utils/formatDate';

const SerialCard = ({ serial }) => {
    return (
        <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12 term-col">
            <article className='movie-card'>
                <a href={`/series/${serial._id}`} className="img-layer"><img src={`${config.localapi}/thumbnails/${serial.thumbnail}`} /></a>
                <h2>
                    <a href={`/series/${serial._id}`}>
                        {serial.title}
                    </a>
                </h2>
                <span>
                    <a className='director-link' href={`/directors/${serial.director._id}`}>
                        {serial.director.fullname}
                    </a>
                </span>
                <i>{formatDate(serial.datePublished).getFullYear()}</i>
            </article>
        </div>
    );
}

export default SerialCard;