import { toast } from "material-react-toastify";
import { editUser } from "../Services/userService";
import { getUser } from "../Services/usersService";

export const setUser = user => {
    return async dispatch => {
        await dispatch({ type: "SET_USER", payload: user });
    }
}

export const clearUser = () => {
    return async dispatch => {
        await dispatch({ type: "CLEAR_USER", payload: "" });
    }
}

export const updateUser = (user, userId) => {
    return async(dispatch, getState) => {
        const USER = {...getState().user };

        try {
            const { data, status } = await editUser(user, userId);

            if (status === 200) {
                await dispatch({ type: "UPDATE_USER", payload: data.user });
                toast.success("مشخصات کاربری شما با موفقیت ویرایش شد.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "UPDATE_USER", payload: USER });
            toast.error("عملیات ویرایش مشخصات کاربری شما با مشکل مواجه شد !");
        }
    }
}