import { toast } from "material-react-toastify";
import { editMovie, getMovies, newMovie, removeMovie } from "../Services/movieService"

export const getAllMovies = (searchText, genre, sort, director, actor, channel) => {
    return async dispatch => {
        const { data } = await getMovies(searchText, genre, sort, director, actor, channel);
        await dispatch({ type: "GET_MOVIES", payload: data.movies });
    }
}

export const addMovie = movie => {
    return async(dispatch, getState) => {
        const movies = [...getState().movies];

        try {
            const { data, status } = await newMovie(movie);

            if (status === 201) {
                await dispatch({ type: "ADD_MOVIE", payload: data.movies });
                toast.success("فیلم با موفقیت اضافه شد");
            }
        } catch (err) {
            await dispatch({ type: "ADD_MOVIE", payload: movies });
            toast.error("عملیات اضافه کردن فیلم با مشکل مواجه شد");
            console.log(err);
        }
    }
}

export const updateMovie = (movie, movieId) => {
    return async(dispatch, getState) => {
        const movies = [...getState().movies];

        try {
            const { data, status } = await editMovie(movieId, movie);

            if (status === 200) {
                await dispatch({ type: "UPDATE_MOVIE", payload: data.movies });
                toast.success("فیلم مورد نظر با موفقیت ویرایش شد");
            }
        } catch (err) {
            await dispatch({ type: "UPDATE_MOVIE", payload: movies });
            toast.error("عملیات ویرایش کتاب با مشکل مواجه شد !");
            console.log(err);
        }
    }
}

export const deleteMovie = movieId => {
    return async(dispatch, getState) => {
        const movies = [...getState().movies];

        try {
            const { data, status } = await removeMovie(movieId);

            if (status === 200) {
                await dispatch({ type: "REMOVE_MOVIE", payload: data.movies });
                toast.success("فیلم مورد نظر با موفقیت پاک شد.");
            }
        } catch (err) {
            await dispatch({ type: "REMOVE_MOVIE", payload: movies });
            console.log(err);
            toast.error("عملیات پاکسازی فیلم با مشکل مواجه شد!");
        }
    }
}