import { toast } from "material-react-toastify";
import { editLimit, getLimits, newLimit, removeLimit } from "../Services/ageLimitService";

export const getAllLimits = searchText => {
    return async dispatch => {
        try {
            const { data } = await getLimits(searchText);
            await dispatch({ type: "GET_LIMITS", payload: data.limits });
        } catch (err) {
            console.log(err);
            toast.error("بارگذاری محدودیت ها با مشکل مواجه شد !");
        }
    }
}

export const addLimit = limit => {
    return async(dispatch, getState) => {
        const limits = [...getState().limits];

        try {
            const { data, status } = await newLimit(limit);

            if (status === 201) {
                await dispatch({ type: "ADD_LIMIT", payload: data.limits });
                toast.success("محدودیت مورد نظر با موفقیت اضافه شد !");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "ADD_LIMIT", payload: limits });
            toast.error("عملیات اضافه کردن محدودیت با مشکل مواجه شد !");
        }
    }
}

export const updateLimit = (limitId, limit) => {
    return async(dispatch, getState) => {
        const limits = [...getState().limits];

        try {
            const { data, status } = await editLimit(limitId, limit);

            if (status === 200) {
                await dispatch({ type: "EDIT_LIMIT", payload: data.limits });
                toast.success("ویرایش محدودیت مورد نظر موفقیت آمیز بود.");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "EDIT_LIMIT", payload: limits });
            toast.error("عملیات ویرایش محدودیت با مشکل مواجه شد !");
        }
    }
}

export const deleteLimit = limitId => {
    return async(dispatch, getState) => {
        const limits = [...getState().limits];

        try {
            const { data, status } = await removeLimit(limitId);

            if (status === 200) {
                await dispatch({ type: "DELETE_LIMIT", payload: data.limits });
                toast.success("محدودیت مورد نظر با موفقیت پاک شد.");
            } else if (status === 404) {
                toast.error("محدودیت مورد نظر پیدا نشد !");
            }
        } catch (err) {
            console.log(err);
            await dispatch({ type: "DELETE_LIMIT", payload: limits });
            toast.error("عملیات پاکسازی محدودیت با مشکل مواجه شد !");
        }
    }
}