import React from 'react'
import SerialCard from '../Card/SerialCard';

const PopularSeries = ({ series }) => {
    return (
        <section className="terms-items">
            <header>
                <h2>محبوب ترین سريال ها</h2>
                <a href="/series">
                    مشاهده همه سريال ها
                </a>
            </header>
            <div className="row">
                {series.map(serial => <SerialCard serial={serial} />)}
            </div>
        </section>
    );
}

export default PopularSeries;