import React, { Fragment, useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import ActorCard from '../Card/ActorCard';
import Pagination from '../common/Pagination';
import { getAllActors } from '../../actions/Actors';
import { paginate } from '../../utils/paginate';

const Actors = () => {
    const dispatch = useDispatch();
    const actors = useSelector(state => state.actors);

    const [currentPage,
        setCurrentPage] = useState(1);
    const [perPage,
        setPerPage] = useState(8);

    const handlePageChange = page => {
        setCurrentPage(page);
    }

    useEffect(() => {
        dispatch(getAllActors());
    }, []);

    const indexedActors = paginate(actors, currentPage, perPage);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | بازیگران
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/actors">بازيگران</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-categories">
                    <div className="row">
                        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                            <section className="terms-items">
                                <div className="row">
                                    {indexedActors.length > 0
                                        ? indexedActors.map(actor => <ActorCard actor={actor} key={actor._id} />)
                                        : (
                                            <div className="alert alert-info">
                                                <p>بازیگری جهت نمایش وجود ندارد !</p>
                                            </div>
                                        )
                                    }
                                </div>

                                {indexedActors.length > 0
                                    ? <Pagination
                                        dataLength={actors.length}
                                        currentPage={currentPage}
                                        perPage={perPage}
                                        onPageChange={handlePageChange} />
                                    : (null)}

                                <br />
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default Actors;