import http from './httpService';
import config from './config.json';

const local = config.localapi; // Local Host URL
const online = config.onlineapi; // Onlice Server URL

// Gets All Directors
export const getDirectors = searchText => {
    return http.get(`${local}/directors?search=${searchText}`);
}

// Gets Single Director
export const getDirector = directorId => {
    return http.get(`${local}/directors/${directorId}`);
}

// Add Director
export const newDirector = director => {
    return http.post(`${local}/admin/directors/add-director`, director, {
        headers: {
            "content-Type": "multipart/form-data"
        }
    });
}

// Edit Director
export const editDirector = (director, directorId) => {
    return http.put(`${local}/admin/directors/edit-director/${directorId}`, director);
}

// Remove Director
export const removeDirector = directorId => {
    return http.delete(`${local}/admin/directors/remove-director/${directorId}`);
}