import http from './httpService';
import config from './config.json';

const local = config.localapi; // LOCALHOST URL
const online = config.onlineapi; // ONLINE SERVER URL

// Register
export const registerUser = user => {
    return http.post(`${local}/user/register`, JSON.stringify(user));
}

// Login
export const loginUser = userInfo => {
    return http.post(`${local}/user/login`, JSON.stringify(userInfo));
}

// Edit User
export const editUser = (user, userId) => {
    return http.put(`${local}/user/edit-user/${userId}`, user);
}

// Forget Password
export const forgetPassword = userEmail => {
    return http.post(`${local}/user/forget-password`, JSON.stringify(userEmail));
}

// Reset Password
export const resetPassword = (passwords, token) => {
    return http.put(`${local}/user/reset-password/${token}`, JSON.stringify(passwords), {
        headers: {
            "Content-Type": "application/json"
        }
    });
}