import React, { Fragment, useRef, useState } from 'react'
import { Helmet } from 'react-helmet';
import { forgetPassword } from '../../Services/userService';
import { toast } from 'material-react-toastify';
import { Redirect, withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { useSelector } from 'react-redux';
import { random } from 'lodash';
// import { decodeToken } from '../../utils/decodeToken';

const ForgotPassword = ({ history }) => {
    const user = useSelector(state => state.user);

    const [email,
        setEmail] = useState("");
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: " پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 5 کاراکتر باشد!",
            email: "ایمیل وارد شده معتبر نمی باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const handleForgotPassword = async event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                const { status } = await forgetPassword({ email });

                if (status === 200) {
                    toast.success("ایمیل بازنشانی رمز با موفقیت ارسال شد.");
                }
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 100));
            }
        } catch (err) {
            toast.error(JSON.parse(err.request.response).message);
            console.log(err);
        }
    }

    if (user.fullname)
        return <Redirect to={"/"} />

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | فراموشی رمز عبور
                </title>
            </Helmet>

            <div className="container">
                <nav aria-label="breadcrumb">
                    <ul className="breadcrumb">
                        <li className="breadcrumb-item">
                            <a href="/">مووی لند</a>
                        </li>
                        <li className="breadcrumb-item active">
                            <a href="/user/forgot-password">فراموشی رمز عبور</a>
                        </li>
                    </ul>
                </nav>
            </div>

            <main className="client-page">

                <div className="container-content">

                    <header>
                        <h2>
                            فراموشی رمز عبور
                        </h2>
                    </header>

                    <div className="form-layer">

                        <form onSubmit={e => handleForgotPassword(e)}>

                            <div className="input-group">
                                <span className="input-group-text" id="email-address">
                                    ایمیل
                                </span>
                                <input
                                    type="text"
                                    name='email'
                                    value={email}
                                    onChange={e => {
                                        setEmail(e.target.value);
                                        validator
                                            .current
                                            .showMessageFor("email");
                                    }}
                                    className="form-control"
                                    placeholder="@"
                                    aria-describedby="email-address" />
                            </div>
                            {validator
                                .current
                                .message("email", email, "required|email")}

                            <div className="input-group">
                                <div
                                    className="g-recaptcha"
                                    data-sitekey="6LdM7HghAAAAANM-6frxXMB7CW7xm1IAnU7Af9Z9"
                                    data-theme="dark"></div>
                            </div>

                            <button className="btn btn-success">
                                ارسال ایمیل بازنشانی
                            </button>

                        </form>
                    </div>

                </div>
            </main>
        </Fragment>
    );
}

export default withRouter(ForgotPassword);