import { random } from 'lodash';
import { toast } from 'material-react-toastify';
import React, { Fragment, useEffect, useRef, useState } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import SimpleReactValidator from 'simple-react-validator';
import { getSingleMessage } from '../../../actions/Message';
import { answerToMessage } from '../../../actions/Messages';
import { idValidator } from '../../../utils/idValidator';

const AnswerMessage = ({ match, history }) => {
    const dispatch = useDispatch();
    const messageId = match.params.id;

    const message = useSelector(state => state.message);

    const [answer,
        setAnswer] = useState("");
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: " پر کردن این فیلد الزامی می باشد!",
            min: "نباید کمتر از 5 کاراکتر باشد!"
        },
        element: message => (
            <div className='alert alert-danger'>
                <p>{message}</p>
            </div>
        )
    }));

    const handleAnswerMessage = async event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                dispatch(answerToMessage(messageId, { answer }));
                history.replace("/admin/messages");
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(0, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error(JSON.parse(err.request.response).message);
        }
    }

    useEffect(() => {
        if (idValidator(messageId)) {
            dispatch(getSingleMessage(messageId));
        } else {
            return location.replace("/admin/messages");
        }
    }, []);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | پاسخ دهی پیام
                </title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        پاسخدهی پیام
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <form className='row g-3' onSubmit={e => handleAnswerMessage(e)}>
                                    <div className="input-group">
                                        <span class="input-group-text" id="basic-addon1">متن پيام</span>
                                        <textarea
                                            className="form-control"
                                            name='message'
                                            value={message.message}
                                            disabled></textarea>
                                    </div>
                                    <br />

                                    <div className="input-group">
                                        <span class="input-group-text" id="basic-addon1">متن پاسخ</span>
                                        <textarea
                                            className="form-control"
                                            placeholder="..."
                                            name='answer'
                                            value={answer}
                                            onChange={e => {
                                                setAnswer(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("answer");
                                            }}></textarea>
                                    </div>
                                    {validator
                                        .current
                                        .message("answer", answer, "required|min:4")}
                                    <br />

                                    <div className="input-group">
                                        <div
                                            className="g-recaptcha"
                                            data-sitekey="6LdM7HghAAAAANM-6frxXMB7CW7xm1IAnU7Af9Z9"
                                            data-theme="dark"></div>
                                        <br />
                                    </div>

                                    <div className="input-group">
                                        <button type='submit' className='btn btn-success col-md-3'>
                                            ثبت پاسخ
                                        </button>
                                        <button
                                            className="btn btn-warning col-md-3"
                                            style={{
                                                marginRight: "10px"
                                            }}
                                            onClick={() => history.push("/admin/messages")}>
                                            انصراف
                                        </button>
                                    </div>
                                </form>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(AnswerMessage);