import { toast } from 'material-react-toastify';
import React, { Fragment, useRef } from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import SimpleReactValidator from 'simple-react-validator';
import { updateMovie } from '../../../actions/Movies';
import { getSingleMovie } from '../../../actions/Movie';
import { getAllDirectors } from '../../../actions/Directors';
import { getAllActors } from '../../../actions/Actors';
import { random } from 'lodash';
import { Helmet } from 'react-helmet';
import config from '../../../Services/config.json';
import { idValidator } from '../../../utils/idValidator';
import { getAllLimits } from '../../../actions/AgeLimits';
import { getAllGenres } from '../../../actions/Genres';
import { getAllChannels } from '../../../actions/Channels';

const EditMovie = ({ match, history }) => {
    const dispatch = useDispatch();
    const movieId = match.params.id;

    const movie = useSelector(state => state.movie);
    const directors = useSelector(state => state.directors);
    const Actrs = useSelector(state => state.actors);
    const genres = useSelector(state => state.genres);
    const channels = useSelector(state => state.channels);
    const ageRestrictions = useSelector(state => state.limits);

    const [title,
        setTitle] = useState("");
    const [shortInfo,
        setShortInfo] = useState(movie.shortInfo);
    const [genre,
        setGenre] = useState(movie.genre);
    const [channel,
        setChannel] = useState(movie.channel);
    const [director,
        setDirector] = useState(movie.director);
    const [actors,
        setActors] = useState(movie.actors);
    const [datePublished,
        setDatePublished] = useState(movie.datePublished);
    const [ageRestriction,
        setAgeRestriction] = useState(movie.ageRestriction);
    const [imdbRating,
        setImdbRating] = useState(movie.imdbRating);
    const [timeLength,
        setTimeLength] = useState(movie.timeLength);
    const [,
        forceUpdate] = useState(0);

    const validator = useRef(new SimpleReactValidator({
        messages: {
            required: "پر کردن این فیلد الزامی می باشد",
            min: "کمتر از 5 کاراکتر نباید باشد"
        },
        element: message => <div
            className='alert alert-danger'
            style={{
                marginTop: "5px"
            }}>
            {message}
        </div>
    }));

    const handleEditMovie = event => {
        event.preventDefault();

        try {
            if (validator.current.allValid()) {
                const movie = new FormData();
                movie.append("title", title);
                movie.append("shortInfo", shortInfo);
                movie.append("genre", genre);
                movie.append("director", director);
                movie.append("actors", actors);
                movie.append("timeLength", timeLength);
                movie.append("imdbRating", imdbRating);
                movie.append("ageRestriction", ageRestriction);
                movie.append("datePublished", datePublished);
                movie.append("thumbnail", document.getElementById("movie-thumbnail").files[0]);

                dispatch(updateMovie(movie, movieId));
                history.replace(`/admin/movies`);
            } else {
                validator
                    .current
                    .showMessages();
                forceUpdate(random(1, 1000));
            }
        } catch (err) {
            console.log(err);
            toast.error(JSON.parse(err.request.response).message);
        }
    }

    useEffect(() => {
        if (idValidator(movieId)) {
            dispatch(getSingleMovie(movieId));
            dispatch(getAllDirectors());
            dispatch(getAllActors());
            dispatch(getAllLimits());
            dispatch(getAllGenres());
            dispatch(getAllChannels());
        } else {
            return location.replace("/admin/movies");
        }
    }, [])

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | ویرایش فیلم
                </title>
            </Helmet>
            <div className="container">
                <section className="term-content">
                    <header
                        style={{
                            padding: "20px",
                            fontSize: "18px"
                        }}>
                        ویرایش فیلم
                    </header>
                    <div className="row">
                        <div className="course-container col-md-12 col-sm-12 col-xs-12 pull-left">
                            <section
                                className="term-description"
                                style={{
                                    padding: "20px"
                                }}>
                                <form className='row g-3' onSubmit={e => handleEditMovie(e)}>
                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">عنوان</span>
                                        <input
                                            type="text"
                                            name='title'
                                            value={title}
                                            placeholder={movie.title}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setTitle(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("title");
                                            }} />
                                    </div>
                                    {validator
                                        .current
                                        .message("title", title, "required|min:4")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">توضیحات</span>
                                        <textarea
                                            type="text"
                                            name='shortInfo'
                                            value={shortInfo}
                                            placeholder={movie.shortInfo}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setShortInfo(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("shortInfo");
                                            }}></textarea>

                                    </div>
                                    {validator
                                        .current
                                        .message("shortInfo", shortInfo, "required|min:10")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">کارگردان</span>
                                        <select
                                            name="director"
                                            id="directors"
                                            className="form-select"
                                            onChange={e => {
                                                setDirector(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("director");
                                            }}>
                                            <option value={"placeholder"}>انتخاب کنید</option>
                                            {directors.map(d => movie.director._id === d._id
                                                ? (
                                                    <option key={d._id} value={d._id} selected>{d.fullname}</option>
                                                )
                                                : (
                                                    <option key={d._id} value={d._id}>{d.fullname}</option>
                                                ))}
                                        </select>
                                    </div>
                                    {validator
                                        .current
                                        .message("director", director, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">بازیگران</span>
                                        <select
                                            name="actor"
                                            id="actors"
                                            className="form-select"
                                            onChange={e => {
                                                setActors(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("actor");
                                            }}>
                                            <option value={"placeholder"}>انتخاب کنید</option>
                                            {Actrs.map(actr => movie.actors.map(a => actr._id === a._id
                                                ? (
                                                    <option key={actr._id} value={actr._id} selected>{actr.fullname}</option>
                                                )
                                                : (
                                                    <option key={actr._id} value={actr._id}>{actr.fullname}</option>
                                                )))}
                                        </select>
                                    </div>
                                    {validator
                                        .current
                                        .message("actor", actors, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">ژانر</span>
                                        <select
                                            name="genre"
                                            id="genres"
                                            className="form-select"
                                            onChange={e => {
                                                setGenre(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("genre");
                                            }}>
                                            <option value={"placeholder"}>انتخاب کنید</option>
                                            {genres.map(g => movie.genre === g._id
                                                ? (
                                                    <option key={g._id} value={g._id} selected>{g.title}</option>
                                                )
                                                : (
                                                    <option key={g._id} value={g._id}>{g.title}</option>
                                                ))}
                                        </select>
                                    </div>
                                    {validator
                                        .current
                                        .message("genre", genre, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">شبکه / پلتفرم</span>
                                        <select
                                            name="channel"
                                            id="channels"
                                            className="form-select"
                                            onChange={e => {
                                                setChannel(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("channel");
                                            }}>
                                            <option value="placeholder">انتخاب کنید</option>
                                            {channels.map(c => movie.channel === c._id
                                                ? (
                                                    <option key={c._id} value={c._id} selected>{c.title}</option>
                                                )
                                                : (
                                                    <option key={c._id} value={c._id}>{c.title}</option>
                                                ))}
                                        </select>
                                    </div>
                                    {validator
                                        .current
                                        .message("channel", channel, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">محدودیت سنی</span>
                                        <select
                                            name="ageRestriction"
                                            id="ageRestrictions"
                                            className="form-select"
                                            onChange={e => {
                                                setAgeRestriction(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("ageRestriction");
                                            }}>
                                            {ageRestrictions.map(a => movie.ageRestriction === a._id
                                                ? (
                                                    <option key={a._id} value={a._id} selected>{a.title}</option>
                                                )
                                                : (
                                                    <option key={a._id} value={a._id}>{a.title}</option>
                                                ))}
                                        </select>
                                    </div>
                                    {validator
                                        .current
                                        .message("ageRestriction", ageRestriction, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">تاریخ انتشار</span>
                                        <input
                                            type="date"
                                            name='datePublished'
                                            value={datePublished}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setDatePublished(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("datePublished");
                                            }} />

                                    </div>
                                    {validator
                                        .current
                                        .message("datePublished", datePublished, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">نمره ی IMDB</span>
                                        <input
                                            type="number"
                                            min={0}
                                            max={10}
                                            name='imdbRating'
                                            value={imdbRating}
                                            placeholder={movie.imdbRating}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setImdbRating(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("imdbRating");
                                            }} />

                                    </div>
                                    {validator
                                        .current
                                        .message("imdbRating", imdbRating, "required")}
                                    <br />

                                    <div className="input-group">
                                        <span className="input-group-text" id="basic-addon1">مدت زمان (دقیقه)</span>
                                        <input
                                            type="number"
                                            min={0}
                                            name='timeLength'
                                            value={timeLength}
                                            placeholder={movie.timeLength}
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            onChange={e => {
                                                setTimeLength(e.target.value);
                                                validator
                                                    .current
                                                    .showMessageFor("timeLength");
                                            }} />

                                    </div>
                                    {validator
                                        .current
                                        .message("timeLength", timeLength, "required")}
                                    <br />

                                    <div className="input-group">
                                        <div className='alert alert-info'>
                                            <a target={'_blank'} href={`${config.localapi}/thumbnails/${movie.thumbnail}`}>مشاهده ي كاور فيلم</a>
                                        </div>
                                    </div>

                                    <div className="input-group">
                                        <label class="input-group-text" for="inputGroupFile01">پوستر فیلم</label>
                                        <input
                                            type="file"
                                            name='thumbnail'
                                            className="form-control"
                                            aria-describedby="basic-addon1"
                                            id='movie-thumbnail' />
                                    </div>
                                    <br />

                                    <div className="input-group">
                                        <button type='submit' className='btn btn-success col-md-3'>
                                            تایید
                                        </button>
                                        <button
                                            className='btn btn-warning col-md-3'
                                            onClick={() => history.push("/admin")}
                                            style={{
                                                marginRight: "10px"
                                            }}>
                                            انصراف
                                        </button>
                                    </div>
                                </form>
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default withRouter(EditMovie);