export const seriesReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_SERIES":
            return [...action.payload];
        case "ADD_SERIAL":
            return [...action.payload];
        case "UPDATE_SERIAL":
            return [...action.payload];
        case "REMOVE_SERIAL":
            return [...action.payload];
        default:
            return state;
    }
}