import React from 'react'
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { deleteGenre } from '../../../actions/Genres';
import { idValidator } from '../../../utils/idValidator';

const DeleteGenre = ({ history, match }) => {
    const dispatch = useDispatch();
    const genreId = match.params.id;

    useEffect(() => {
        if (idValidator(genreId)) {
            if (confirm("آیا از حذف کردن این ژانر مطئنید ؟")) {
                dispatch(deleteGenre(genreId));
                history.replace("/admin/genres");
            }
            else {
                history.replace("/admin/genres");
            }
        } else {
            return location.replace("/admin/genres");
        }
    }, []);

    return null;
}

export default withRouter(DeleteGenre);