export const moviesReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_MOVIES":
            return [...action.payload];
        case "ADD_MOVIE":
            return [...action.payload];
        case "UPDATE_MOVIE":
            return [...action.payload];
        case "REMOVE_MOVIE":
            return [...action.payload];
        default:
            return state;
    }
}