import React, { Fragment, useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import DirectorCard from '../Card/DirectorCard';
import Pagination from '../common/Pagination';
import { getAllDirectors } from '../../actions/Directors';
import { paginate } from '../../utils/paginate';

const Directors = () => {
    const dispatch = useDispatch();
    const directors = useSelector(state => state.directors);

    const [currentPage,
        setCurrentPage] = useState(1);
    const [perPage,
        setPerPage] = useState(8);

    const handlePageChange = page => {
        setCurrentPage(page);
    }

    useEffect(() => {
        dispatch(getAllDirectors());
    }, []);

    const indexedDirectors = paginate(directors, currentPage, perPage);

    return (
        <Fragment>
            <Helmet>
                <title>
                    مووی لند | کارگردانان
                </title>
            </Helmet>
            <div className="container">

                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/">مووی لند</a>
                            </li>
                            <li className="breadcrumb-item active">
                                <a href="/directors">كارگردانان</a>
                            </li>
                        </ul>
                    </nav>
                </div>

                <section className="term-categories">
                    <div className="row">
                        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                            <section className="terms-items">
                                <div className="row">
                                    {indexedDirectors.length > 0
                                        ? indexedDirectors.map(director => <DirectorCard director={director} key={director._id} />)
                                        : (
                                            <div className="alert alert-info">
                                                <p>کارگردانی جهت نمایش وجود ندارد !</p>
                                            </div>
                                        )}
                                </div>

                                {indexedDirectors.length > 0
                                    ? <Pagination
                                        dataLength={directors.length}
                                        currentPage={currentPage}
                                        perPage={perPage}
                                        onPageChange={handlePageChange} />
                                    : (null)}

                                <br />
                            </section>
                        </div>
                    </div>
                </section>
            </div>
        </Fragment>
    );
}

export default Directors;