import React, { Fragment } from 'react'
import AdminFooter from '../Admin/common/AdminFooter';
import AdminNavbar from '../Admin/common/AdminNavbar';

const AdminLayout = ({ children }) => {
    return (
        <Fragment>
            <header>
                <AdminNavbar />
            </header>

            <main style={{
                padding: "30px"
            }}>
                {children}
            </main>

            <AdminFooter />
        </Fragment>
    );
}

export default AdminLayout;