export const actorsReducer = (state = [], action) => {
    switch (action.type) {
        case "GET_ACTORS":
            return [...action.payload];
        case "ADD_ACTOR":
            return [...action.payload];
        case "UPDATE_ACTOR":
            return [...action.payload];
        case "REMOVE_ACTOR":
            return [...action.payload];
        default:
            return state;
    }
}